import React, { Component } from 'react'
import UserDataService from '../../api/RestApi/UserDataService'
import './ViewAllAdmin.css'
import "bootstrap/dist/css/bootstrap.min.css";
import '../../bootstrap.css';
import Moment from 'react-moment';

import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap'


class ViewAllManager extends Component {
    constructor(props) {
        super(props);
        this.state = {
          interviewers: [],
          getInterviewerList:false,
          modalupdateisOpen: false,
         
          name: '',
          userType: '',
          contactNumber: '',
          user_type: '',
          userGroup:'',
          department: '',
          designation: '',
          dateOfBirth: '',
          experience: '',
          nullMessage:''
         

        }
    }
    componentDidMount(){
        UserDataService.retrieveAllManager().then(
            response => {
              if(response.data.dataList.length === 0){
              this.setState({ 
                getInterviewerList:false,
                nullMessage:'No Record Found'
              })
            }
            else{
              this.setState({interviewers: response.data.dataList ,
                getInterviewerList:true
            })
             
            }
              
            }).catch(response=>{
                this.setState({
                    getInterviewerList:false
                })
            })
        }

        getInterviewer = (userId,user_type) => {
          UserDataService.retrieveUserDetails(userId).then(
            response => {
              console.log(response.data.dateOfBirth);
              this.setState({
                name: response.data.name, contactNumber: response.data.contactNumber,
                dateOfBirth: response.data.dateOfBirth, experience: response.data.experience,
                department: response.data.department, designation: response.data.designation,
                userGroup:response.data.userGroup,
                user_type: user_type, modalupdateisOpen: !this.state.modalupdateisOpen,buttonDisable:true
              })
            }
          )
          }
   
  toggleCloseupdateModal = () => {

    this.setState({ modalupdateisOpen: !this.state.modalupdateisOpen })
  }

  onChange = (event) => {
    this.props.history.push(`/${event.target.value}`);
  }
    render() {
        
        let interviewerRow = this.state.interviewers.map((interviewers) => {
           
            return (
               
              <tr key={interviewers.userDetailsId}>
                  
                 <td></td>
                <td>{interviewers.name}</td>
                <td x-ms-format-detection="none">{interviewers.contactNumber}</td>
                <td>{interviewers.department}</td>
                <td>{interviewers.designation}</td>
                <td><Moment format="DD-MM-YYYY" date={interviewers.dateOfBirth}></Moment></td>
                <td>{interviewers.experience}</td>
                <td><a type="button" className="fa fa-eye fa-2x" onClick={this.getInterviewer.bind(this, interviewers.user.userId,interviewers.user.user_type)} data-toggle="tooltip" data-placement="top"
							title="View interviewer"></a></td>
                
          <Modal isOpen={this.state.modalupdateisOpen}>
            <ModalHeader toggle={this.toggleCloseupdateModal.bind(this)}>
              View Manager
            </ModalHeader>
            <ModalBody>
              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align"
                  htmlFor="name"> Name : </label>
                <input type="text" name="name" placeholder="Enter name" value={this.state.name}
                 className="col-form-label col-md-7 col-sm-7" id="name" readOnly={true} />
                 

              </div>
              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align"
                  htmlFor="contactNumber"> Contact Number : </label>
                <input type="text" name="contactNumber" placeholder="Enter contact number" value={this.state.contactNumber}
                className="col-form-label col-md-7 col-sm-7" id="contactNumber"readOnly={true}/>
               

              </div>
              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align"
                  htmlFor="dateOfBirth"> Date Of Birth : </label>
                <input type="date" name="dateOfBirth" placeholder="Enter date of birth" value={this.state.dateOfBirth}
                 className="col-form-label col-md-7 col-sm-7" id="dateOfBirth" readOnly={true}  />
                 
              </div>
              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align"
                  htmlFor="experience"> Experience <small className="text-muted">(in years)</small> :</label>
                <input type="text" name="experience" placeholder="Enter experience" value={this.state.experience}
                 maxLength="5" className="col-form-label col-md-7 col-sm-7" id="experience" readOnly={true}/>
                 
              </div>
              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align" htmlFor="user_type">User Type : </label>

                <select id="user_type" className="user_type popupSelect"  disabled={true}>
                  <option value={this.state.user_type}>{this.state.user_type} </option>
                </select>
              </div>
              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align" htmlFor="userGroup">Group : </label>

                <select id="userGroup" className="user_type popupSelect"  disabled={true}>
                  <option value={this.state.userGroup}>{this.state.userGroup} </option>
                </select>
              </div>

              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align" htmlFor="department">Department :  </label>
                <select className="popupSelect" id="department" disabled={true} >
                <option value={this.state.department} >{this.state.department} </option>
                </select>
              </div>
              <div>
              </div>
              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align" htmlFor="designation">Designation : </label>

                <select className="popupSelect" name="designation" id="designation" disabled={true}>
                <option value={this.state.designation} >{this.state.designation} </option>

                </select>
              </div>

            </ModalBody>
            <ModalFooter>
              <Button onClick={this.toggleCloseupdateModal.bind(this)} type="button" className="btn btn-default" >Close</Button>
            </ModalFooter>
          </Modal>

                </tr>
      );
    });
    return (
      <div>
        
        {/* <div className="col-md-12 col-sm-12 size">         */}
               
               <h1 align="center" id='titleOfFilterPages'> Manager List</h1> 

            {/* </div>  */}
        
       
       
        <div className="col-md-12 col-sm-12">
        {!this.state.getInterviewerList && <div>
          <select onChange={this.onChange} className="onSelect" >
                    <option>Filter</option>
                    <option value="viewAllUser">All</option>
                    <option value="viewAllAdmin">Admin</option>
                    <option value="viewAllHumanResource">HumanResource</option>
                    <option value="viewAllInterviewer">Interviewer</option>
                    <option value="viewAllManager">Manager</option>

                  </select> </div>}
        {!this.state.getInterviewerList && <div>
              <h1 align="center" id='title1'>{this.state.nullMessage}</h1>
              
        </div>}
        {this.state.getInterviewerList && <div className="viewAllAdminTable">
        <div >
                  <select onChange={this.onChange} className="onSelect" >
                    <option>Filter</option>
                    <option value="viewAllUser">All</option>
                    <option value="viewAllAdmin">Admin</option>
                    <option value="viewAllHumanResource">HumanResource</option>
                    <option value="viewAllInterviewer">Interviewer</option>
                    <option value="viewAllManager">Manager</option>
                  </select> 
                  </div>
          <table className="table" id="viewAllAdminTable">
            <thead>
              <tr >
                <th align="center">Index</th>
                <th align="center"> Name </th>
                <th align="center"> Contact number </th>
                <th align="center"> Department</th>
                <th align="center"> Designation </th>
                <th align="center"> Date of Birth </th>
                <th align="center"> Experience </th>
                <th align="center">View Details</th>
               
                
              </tr>
            </thead>
            <tbody>
              {interviewerRow}
               
            </tbody>
          </table>
        </div>}
        </div>
  </div>
  
    )
  }
}
export default ViewAllManager