import React from 'react';
import { Pie, Doughnut} from 'react-chartjs-2';
import CandidateDataService from '../../api/RestApi/CandidateDataService.js'
import './viewAllUserChart.css'



export default class CandidateQualificationChart extends React.Component {

    constructor() {
        super();
        this.state = {
          candidates: [],
          userId: '',
          getCandidateList: false,
          nullMessage: '',
          status:'',
          statusDepartment:'',
          i:'',
          count:'',
          totalCount:0,
          candidate_BE_Count:0,
          candidate_BCA_Count:0,
          candidate_MCA_Count:0,
          candidate_ME_Count:0,
          candidate_MBA_Count:0,
          candidate_BCOM_Count:0,
          totalDepartmentCount:0,
          digitalCount:0,
          qaCount:0,
         dftCount:0,
          verificationCount:0,
          humanResourceCount:0,

          
        }
      }


    componentDidMount() {
      CandidateDataService.retrieveAllCandidates().then(response => {
          if(response.data.dataList.length !=0){
          this.setState({
            candidates: response.data.dataList,
            getCandidateList: true,

           
          })
        
        }else{
          this.setState({
            getCandidateList: false,
            nullMessage: "No Record Found",
          })
        }
        }).catch(response => {
          this.setState({
            getCandidateList: false,
            nullMessage: "Something Went Wrong.",
          })
        })
    
      }

  render() {

    let candidateRow = this.state.candidates.map((candidates, i) => {
    
      this.state.totalCount=this.state.totalCount+1;
      this.state.totalDepartmentCount=this.state.totalDepartmentCount+1;
      
       
         if(candidates.qualification === "B.E"){

          this.state.candidate_BE_Count=this.state.candidate_BE_Count+1;
          this.state.totalCount=this.state.totalCount-1;

        }else if(candidates.qualification === "M.E"){

          this.state.candidate_ME_Count=this.state.candidate_ME_Count+1;
          this.state.totalCount=this.state.totalCount-1;

        }else if(candidates.qualification === "B.C.A"){

            this.state.candidate_BCA_Count=this.state.candidate_BCA_Count+1;
            this.state.totalCount=this.state.totalCount-1;

        }
        else if(candidates.qualification === "M.C.A"){

          this.state.candidate_MCA_Count=this.state.candidate_MCA_Count+1;
          this.state.totalCount=this.state.totalCount-1;

        }
        else if(candidates.qualification === "M.B.A"){

            this.state.candidate_MBA_Count=this.state.candidate_MBA_Count+1;
            this.state.totalCount=this.state.totalCount-1;
  
        }
        else if(candidates.qualification === "B.COM"){

            this.state.candidate_BCOM_Count=this.state.candidate_BCOM_Count+1;
            this.state.totalCount=this.state.totalCount-1;
  
          }
          if(candidates.candidateDepartment === "Digital"){

            this.state.digitalCount=this.state.digitalCount+1;
            this.state.totalDepartmentCount=this.state.totalDepartmentCount-1;
  
          }else if(candidates.candidateDepartment === "QA"){
  
            this.state.qaCount=this.state.qaCount+1;
            this.state.totalDepartmentCount=this.state.totalDepartmentCount-1;
  
          }else if(candidates.candidateDepartment === "DFT"){
  
              this.state.dftCount=this.state.dftCount+1;
              this.state.totalDepartmentCount=this.state.totalDepartmentCount-1;
  
          }
          else if(candidates.candidateDepartment === "Verification"){
  
            this.state.verificationCount=this.state.verificationCount+1;
            this.state.totalDepartmentCount=this.state.totalDepartmentCount-1;
  
          }
          else if(candidates.candidateDepartment === "HumanResource"){
  
              this.state.humanResourceCount=this.state.humanResourceCount+1;
              this.state.totalDepartmentCount=this.state.totalDepartmentCount-1;
    
          }
          
            console.log("Digital"+this.state.digitalCount)
            console.log("QA"+this.state.qaCount)
            console.log("DFT"+this.state.dftCount)
            console.log("Verification"+this.state.verificationCount)
            console.log("HumanResource"+ this.state.humanResourceCount)
        
          console.log("BE"+this.state.candidate_BE_Count)
          console.log("ME"+this.state.candidate_ME_Count)
          console.log("BCA"+this.state.candidate_BCA_Count)
          console.log("MCA"+this.state.candidate_MCA_Count)
          console.log("MBA"+this.state.candidate_MBA_Count)
          console.log("BCOM"+this.state.candidate_BCOM_Count)



        return (
            this.state.statusDepartment={
                labels: ['Digital', 'QA', 'DFT','Verification','HumanResource'],
            datasets: [
            {
              
             label: "qualification of candidate",
              
             
            backgroundColor: [
                '#ADD8E6',
                '#2FDE00',
                '#fed8b1',
                '#6800B4',
                '#c2ff9b',
                '#B21F00',
            
            ],
            
            hoverBackgroundColor: [
                '#85b0bd',
                '#175000',
                '#CBAD8E',
                '#35014F',
                '#4B5000',
                '#501800',
            ],
            data: [  this.state.digitalCount,this.state.qaCount,
                this.state.dftCount,this.state.verificationCount,
                this.state.humanResourceCount,
            ],
           
            },
           
              ]
             },
            this.state.status={
                labels: ['B.E', 'M.E', 'B.C.A','M.C.A','M.B.A','B.COM'],
            datasets: [
            {
              
             label: "qualification of candidate",
              
            
            backgroundColor: [
                '#ADD8E6',
                '#2FDE00',
                '#fed8b1',
              
              '#6800B4',
              '#c2ff9b',
              
              
              '#B21F00',
            
            ],
            
            hoverBackgroundColor: [
                '#85b0bd',
                '#175000',
                '#CBAD8E',
                
                '#35014F',
                '#4B5000',
                
                
                '#501800',
            ],
            data: [this.state.candidate_BE_Count,this.state.candidate_ME_Count,
                this.state.candidate_BCA_Count,this.state.candidate_MCA_Count,
                this.state.candidate_MBA_Count,this.state.candidate_BCOM_Count,
            ],
           
            },
           
              ]
             }
        

        
        );
      });
  
      
    return (
      <div className="row">
            <div className="col-md-6"><h1 align="center" id='titlechart'>Department Of Candidate </h1>
        {!this.state.getCandidateList && <div>
              <h1 align="center" id='title'>{this.state.nullMessage}</h1>
              </div>}
      {this.state.getCandidateList &&
        <Pie
        
          data={this.state.statusDepartment}
          width={"800"}
          height={"200"}
        
          
          
          options={{
            responsive: true, 
            maintainAspectRatio: true,
            // cutoutPercentage:30,
            elements: {
                arc: {
                  borderWidth: 1,
                //   borderColor:'black'
                }
              },
            title:{
              display:false,
              text:'Candidate according department',
              fontSize:25,
              padding:10,
              fontFamily:'Times New Roman' ,
              textAlign:'center',
              fontColor:'black'

              
            
            },
            legend:{
              display:true,
              align:'center',
               position:'right',
             
              
              
            },
            // scales: {
            //   xAxes: [{
            //     ticks: {
            //       //  min: 0,
            //        beginAtZero:true,
            //        stepSize:1,
            //        backdropColor:'#2A3F54',
            //     }
            //   }]
            // }
           
            
          }
        }
        />}
        </div>
     
          
          
          <div className="col-md-6">
          
          <h1 align="center" id='titlechart'>Qualification Of Candidate</h1>
        {!this.state.getCandidateList && <div>
              <h1 align="center" id='title'>{this.state.nullMessage}</h1>
              </div>}
      {this.state.getCandidateList &&
        <Pie
        
          data={this.state.status}
          width={"800"}
          height={"200"}
          
          
          options={{
            responsive: true, 
            maintainAspectRatio: true,
            // cutoutPercentage:30,
            elements: {
                arc: {
                  borderWidth: 1,
                //   borderColor:'black'
                }
              },
            title:{
              display:false,
              text:'Candidate according qualification',
              fontSize:25,
              padding:10,
              fontFamily:'Times New Roman' ,
              textAlign:'center',
              fontColor:'black'

              
            
            },
            legend:{
              display:true,
              align:'center',
               position:'right',
             
              
              
            },
            // scales: {
            //   xAxes: [{
            //     ticks: {
            //       //  min: 0,
            //        beginAtZero:true,
            //        stepSize:1,
            //        backdropColor:'#2A3F54',
            //     }
            //   }]
            // }
           
            
          }
        }
        />}
        </div>
        </div>
     
    );
  }
}

