import axios from 'axios'

class CandidateDataService {

    retrieveAllCandidates(){
       return axios.get('http://localhost:8080/showcandidate');
    }

    retrieveAllInterviewers(){
        return axios.get('http://localhost:8080/showinterviewer');
    }

    deleteCandidate(candidateId){
        return axios.delete(`http://localhost:8080/deletecandidate/${candidateId}`);
    }

    updateCandidate(candidateId,data){
        return axios.put(`http://localhost:8080/updatecandidate/${candidateId}`,data);
    }
    
    retrieveUnscheduledInterview(){
        return axios.get('http://localhost:8080/unscheduledinterview');
     
    }
    retrievePenddingInterviewApproval(){
        return axios.get('http://localhost:8080/getpenddingapproval');
    }

    getCandidate(candidateId){
       return axios.get(`http://localhost:8080/getcandidate/${candidateId}`)
    }

    retrieveScheduleInterview(){
        return axios.get('http://localhost:8080/listofscheduleinterview'); 
    }

    retrieveHireCandidate(){
        return axios.get('http://localhost:8080/listofhirecandidate');
    }
    
    retrieveRejectedCandidate(){
        return axios.get('http://localhost:8080/listofrejectedcandidate');
    }

    todaysInterviewList(){
        return axios.get('http://localhost:8080/gettodaysinterview')
    } 

    listForChart(){
        return axios.get('http://localhost:8080/chartlist')
    } 



    getListOfCandidates(){
        return axios.get('http://localhost:8080/listofcandidate');
    }
    getInfomation(){
        return axios.get('http://localhost:8080/getinformation');
    }
}

export default new CandidateDataService();