import React, { Component } from 'react'
import UserDataService from '../../api/RestApi/UserDataService'
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap'
import "bootstrap/dist/css/bootstrap.min.css";
import '../../bootstrap.css';
import './ViewAllUser.css';
// import viewAllUserChart from '../charts/ViewAllUserChart.jsx';
const group = {

  " ": [
      { id: '', text: 'Select Department' }

  ],
  "Admin": [
      { id: 'Admin', text: 'Admin' }

  ],
  "HumanResource": [
      { id: 'HumanResource', text: 'HumanResource' }

  ],
  "Interviewer": [
      { id: 'PES', text: 'PES' },
      { id: 'ASIC', text: 'ASIC' },
      { id: 'QA', text: 'QA' },
      { id: 'HumanResource', text: 'HumanResource' }

  ],
  "Manager":[
      {id:'PESManager',text:'PES'},
      { id: 'ASICManager', text: 'ASIC' },
      { id: 'AerospaceManager', text: 'Aerospace' },
          
      
  ]
}
const department = {

  " ": [
      { id: '', text: 'Select Department' }

  ],
  "Admin": [
      { id: 'Admin', text: 'Admin' }

  ],
  "HumanResource": [
      { id: 'HumanResource', text: 'HumanResource' }

  ],
  "PES": [
      { id: 'Digital', text: 'Digital' },
      { id: 'QA', text: 'QA' },
      

  ],
  "ASIC": [
      { id: 'DFT', text: 'DFT' },
      { id: 'Verification', text: 'Verification' },
    
  ],
  "QA": [
      { id: 'Automation', text: 'Automation' },
      { id: 'Manual', text: 'Manual' },
      

  ],
  "PESManager": [
      { id: 'DigitalManager', text: 'Digital' },
      { id: 'QAManager', text: 'QA' },
      

  ],
  "ASICManager": [
      { id: 'DFTManager', text: 'DFT' },
      { id: 'VerificationManager', text: 'Verification' },
    
  ],
  "AerospaceManager": [
      { id: 'AutomationManager', text: 'Automation' },
      { id: 'ManualManager', text: 'Manual' },
      

  ],
}

const designation = {
  " ": [
      { id: '', text: 'Select Designation' }

  ],
  "Admin": [
      { id: 'Contractor', text: 'Contractor' },
      { id: 'Admin', text: 'Admin' }

  ],
  "HumanResource": [
      { id: 'HumanResource', text: 'HumanResource' },
      { id: 'HRBP', text: 'HRBP' }

  ],
  "Digital": [
      { id: 'Engineer', text: 'Engineer' },
      { id: 'Senior Engineer', text: 'Senior Engineer' },
      { id: 'Technical Lead', text: 'Technical Lead' },
      { id: 'Technical Manager', text: 'Technical Manager' },
      { id: 'Delivery Manager', text: 'Delivery Manager' }

  ],
  "QA": [
      { id: 'Engineer', text: 'Engineer' },
      { id: 'Senior Engineer', text: 'Senior Engineer' },
      { id: 'Technical Lead', text: 'Technical Lead' },
      { id: 'Technical Manager', text: 'Technical Manager' },
      { id: 'Delivery Manager', text: 'Delivery Manager' }

  ],
  "DFT": [
      { id: 'Engineer', text: 'Engineer' },
      { id: 'Senior Engineer', text: 'Senior Engineer' },
      { id: 'Technical Lead', text: 'Technical Lead' },
      { id: 'Technical Manager', text: 'Technical Manager' },
      { id: 'Delivery Manager', text: 'Delivery Manager' }

  ],
  "Verification": [
      { id: 'Engineer', text: 'Engineer' },
      { id: 'Senior Engineer', text: 'Senior Engineer' },
      { id: 'Technical Lead', text: 'Technical Lead' },
      { id: 'Technical Manager', text: 'Technical Manager' },
      { id: 'Delivery Manager', text: 'Delivery Manager' }

  ],
  "Automation": [
      { id: 'Engineer', text: 'Engineer' },
      { id: 'Senior Engineer', text: 'Senior Engineer' },
      { id: 'Technical Lead', text: 'Technical Lead' },
      { id: 'Technical Manager', text: 'Technical Manager' },
      { id: 'Delivery Manager', text: 'Delivery Manager' }

  ],
  "Manual": [
      { id: 'Engineer', text: 'Engineer' },
      { id: 'Senior Engineer', text: 'Senior Engineer' },
      { id: 'Technical Lead', text: 'Technical Lead' },
      { id: 'Technical Manager', text: 'Technical Manager' },
      { id: 'Delivery Manager', text: 'Delivery Manager' }

  ],
  "DigitalManager": [
      
      { id: 'Technical Manager', text: 'Technical Manager' },
      { id: 'Delivery Manager', text: 'Delivery Manager' }

  ],
  "QAManager": [
     
      { id: 'Technical Manager', text: 'Technical Manager' },
      { id: 'Delivery Manager', text: 'Delivery Manager' }

  ],
  "DFTManager": [
      
      { id: 'Technical Manager', text: 'Technical Manager' },
      { id: 'Delivery Manager', text: 'Delivery Manager' }

  ],
  "VerificationManager":[
      { id: 'Technical Manager', text: 'Technical Manager' },
      { id: 'Delivery Manager', text: 'Delivery Manager' }

  ],
  "AutomationManager": [
     
      { id: 'Technical Manager', text: 'Technical Manager' },
      { id: 'Delivery Manager', text: 'Delivery Manager' }

  ],
  "ManualManager": [
     
      { id: 'Technical Manager', text: 'Technical Manager' },
      { id: 'Delivery Manager', text: 'Delivery Manager' }

  ],
}

class ViewAllUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      users: [],
      modaldeleteisOpen: false,
      modalupdateisOpen: false,
      userid: '',
      name: '',
      userGroup: '',
      contactNumber: '',
      user_type: '',
      department: '',
      designation: '',
      dateOfBirth: '',
      experience: '',
      nameInvalid: '',
      numberInvalid: '',
      dobInvalid: '',
      experienceInvalid:'',
      dataValuegroup: ' ',
      dataValuedepartment: ' ',
      dataValuedesignation: ' ',
      buttonDisable: true,
      nullMessage:'',
      user_group:'',
      user_department:'',
      getUserList:false
    }
  }

  
  onUsertypeChange = (value) => {
    this.setState({ dataValuegroup: value, user_type: value });

  }

onUserGroupChange = ({ target: { value } }) => {
   if(this.state.userGroup === "PES" || "ASIC" || "Aerospace" ){
    this.setState({ dataValuedepartment:value+"Manager",group:value+"Manager",userGroup:value,});
   }
    this.setState({ dataValuedepartment:value,group:value,userGroup:value,});
   
            this.setState({
                buttonDisable:false
            })
            this.concatManagerGroup()
   
}
onDepartmentChange = ({ target: { value } }) => {
  if(this.state.department === "Digital" || "QA" || "DFT" || "Verification" ||"Automation"||"Manual"){
    this.setState({ dataValuedesignation:value+"Manager",department:value+"Manager",department:value,});
   }

    this.setState({ dataValuedesignation: value, department: value });
   
            this.setState({
                buttonDisable:false
            })
            this.concatManagerGroup()
  
}

onDesignationChange = ({ target: { value } }) => {
   
    if(value === ' '){
        //this.setState({ buttonDisable:true });
       
            this.setState({

                buttonDisable:true
            })
      
    }
    else{
        this.setState({ designation: value, designation: value });
        
            this.setState({
                buttonDisable:false

            })
           
           
    }
    this.concatManagerGroup()
}
   handleChange = (event) => {

    this.setState({
      [event.target.name]: event.target.value,
      buttonDisable: false

    })
   
  }
  validateName = (event) => {

    this.setState({
      [event.target.name]: event.target.value,

    })

    let name = this.state.name

    var pattern = /^[a-zA-Z ]{2,30}$/;
    if ((name.match(pattern)) && name.length > 1) {
        this.setState({
            nameInvalid: '',
            buttonDisable:false

        })
    } else {
        this.setState({
            nameInvalid: 'Please enter name',
            buttonDisable:true

        })
    }
    if (name === "") {
        this.setState({
            nameInvalid: 'Please enter name',
            buttonDisable:true

        })
    }
}

validateExperience = (event) => {

  this.setState({
    [event.target.name]: event.target.value,
  })
  this.state.experience = event.target.value

  var experience = this.state.experience

  if ( experience.length >= 0) {
    this.setState({
        experienceInvalid: '',
        buttonDisable:false,
    })
}else{
      this.setState({
          experienceInvalid: 'Please enter experience',
          buttonDisable:true

      })
  }
}

validateMobileNumber = (event) => {

  this.setState({
    [event.target.name]: event.target.value,

  })

  this.state.contactNumber = event.target.value

  var mobile = this.state.contactNumber

  if ( mobile.length > 9) {
      this.setState({
          numberInvalid: '',
          buttonDisable:false,
      })
  } else {
      this.setState({
          numberInvalid: 'Please enter valid mobile number(*9876543210)',
          buttonDisable:true
      })
  }

}
validateDOB = (event) => {
  this.setState({
      [event.target.name]: event.target.value,

      dateOfBirth: event.target.value ,buttonDisable: false}, 
    
  );

  this.state.dateOfBirth = event.target.value

  var today = new Date();
       var date = new Date(this.state.dateOfBirth);
      var diffYears = today.getFullYear() - date.getFullYear();
      console.log(diffYears)
      if (diffYears >= 18) {
                      this.setState({
                          dobInvalid: '',
                          buttonDisable:false
                      })
                  } else {
                      this.setState({
                          dobInvalid: 'User must be 18 years old or above',
                          buttonDisable:true
                      })
                  }
}

allowNumbersForContactNumber(e) {

  if (!(e.ctrlKey || e.altKey
      || (47 < e.keyCode && e.keyCode < 58 && e.shiftKey === false)
      || (95 < e.keyCode && e.keyCode < 106)
      || (e.keyCode === 8) || (e.keyCode === 9)
      || (e.keyCode > 34 && e.keyCode < 40)
      || (e.keyCode === 46))) {
      e.preventDefault();

  }

}
allowNumbersForExperience(event) {

  if (!(event.ctrlKey || event.altKey
      || (47 < event.keyCode && event.keyCode < 58 && event.shiftKey === false)
      || (95 < event.keyCode && event.keyCode < 106)
      || (event.keyCode === 8) || (event.keyCode === 9)
      || (event.keyCode > 34 && event.keyCode < 40)
      || (event.keyCode === 46)|| (event.keyCode === 190)|| (event.keyCode === 110)))
      event.preventDefault();
}

  componentDidMount() {
    UserDataService.retrieveAllUsers().then(
      response => {
        if(response.data.dataList.length === 0){
                
          this.setState({ 
            getUserList:false,
            nullMessage:'No Record Found'
          })
        }
        else{
          this.setState({
            users: response.data.dataList ,
          getUserList:true
        })
        
        }
       
      }).catch(response=>{
          this.setState({
              getUserList:false
          })
      })
  }
  concatManagerGroup=()=>{
    var manager="Manager"
       
    this.state.userGroup = this.state.userGroup.replace(manager,'')
    this.state.department=this.state.department.replace(manager,'')

   
    console.log(this.state.userGroup)
    console.log(this.state.department)
  

  }
  toggleUpdateModal = (userId) => {

    this.setState({ userid: userId })
    
      this.getUserData(userId);
  

  }

  getUserData = (userId) => {
    UserDataService.retrieveUserDetails(userId).then(
      response => {
        console.log(response.data);
        this.setState({
          userId:response.data.user.userId,
          name: response.data.name, contactNumber: response.data.contactNumber,
          dateOfBirth: response.data.dateOfBirth, experience: response.data.experience,
          department: response.data.department, designation: response.data.designation,
          user_type: response.data.user.user_type,userGroup:response.data.userGroup, modalupdateisOpen: !this.state.modalupdateisOpen,buttonDisable:true
        })
       
          this.concatManagerGroup()
        
        
      }
    )
  }

  UpdateUser = () => {
  
if(this.state.user_type==="Manager"){
  this.concatManagerGroup()
}
    UserDataService.updateUser(this.state.userid, this.state).then(response => this.UpdatedUser(response)).catch(response => this.componentDidCatchUpdate(response))
    this.setState({ modalupdateisOpen: !this.state.modalupdateisOpen })
  }

  UpdatedUser = (response) => {
    this.setState({buttonDisable:true})
    alert("User is Updated successfully ");
    
    this.componentDidMount();
  }

  toggleCloseupdateModal = () => {

    this.setState({ modalupdateisOpen: !this.state.modalupdateisOpen })
  }

  toggleDeleteModal = (userId) => {

    this.setState({ modaldeleteisOpen: !this.state.modaldeleteisOpen, userid: userId })
  }

  toggleClosedeleteModal = () => {

    this.setState({ modaldeleteisOpen: !this.state.modaldeleteisOpen })
  }

  deleteUser = () => {

    UserDataService.deleteUser(this.state.userid).then(response => this.deletedUser(response)).catch(response => this.componentDidCatchDelete(response))
    this.setState({ modaldeleteisOpen: !this.state.modaldeleteisOpen })

  }

  deletedUser = (response) => {
    alert("User is deleted successfully ");
    this.componentDidMount();
  }

  componentDidCatchUpdate = (error,info) => {
    console.log(error.response.status)
        if(error.response.status === 409){  
            alert("User already exist. ");
        }

        if(error.response.status === 404){  
            alert("User not found.");
        }
  }

  componentDidCatchDelete = (error) =>{

    if(error.response.status === 404){  
      alert("User not found.");
    }
  }

  onChange = (event) => {
    console.log(event)
     this.props.history.push(`/${event.target.value}`);
  }

  render() {
    const { dataValuegroup } = this.state;
    const optionsgroup = group[dataValuegroup];
    const { dataValuedepartment } = this.state;
    const options = department[dataValuedepartment];
    const { dataValuedesignation } = this.state;
    const optionsdesignation = designation[dataValuedesignation];
    let userRow = this.state.users.map((users) => {
      return (
        
        <tr key={users.userId}>
          <td></td>
          <td>{users.username}</td>
          <td>{users.user_type}</td>
        
          <td><a type="button" onClick={this.toggleUpdateModal.bind(this, users.userId)} data-toggle="tooltip" data-placement="top"
							title="Update user"className="fa fa-edit fa-2x"> </a></td>
          <Modal isOpen={this.state.modalupdateisOpen} onOpened={this.onUsertypeChange.bind(this,this.state.user_type)}>
            <ModalHeader toggle={this.toggleCloseupdateModal.bind(this)}>
              Update User
            </ModalHeader>
            <ModalBody>
              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align"
                  htmlFor="name"> Name : </label>
                <input type="text" name="name" placeholder="Enter name" value={this.state.name}
                  onChange={this.validateName} className="col-form-label col-md-7 col-sm-7" id="name" autoComplete="off" />
                 <span id="name" className='error'>{this.state.nameInvalid}</span>

              </div>
              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align"
                  htmlFor="contactNumber"> Contact Number : </label>
                <input type="text" name="contactNumber" placeholder="Enter contact number" value={this.state.contactNumber}
                  onChange={this.validateMobileNumber} onKeyDown={this.allowNumbersForContactNumber} maxLength="10" className="col-form-label col-md-7 col-sm-7" id="mobilenumber" autoComplete="off"/>
               <span className='error' id="mobilenumber">{this.state.numberInvalid}</span>

              </div>
              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align"
                  htmlFor="dob"> Date Of Birth : </label>
                <input type="date" name="dateOfBirth" placeholder="Enter date of birth" value={this.state.dateOfBirth}
                  onChange={this.validateDOB} className="col-form-label col-md-7 col-sm-7" id="dateOfBirth" autoComplete="off" />
                 <span id="dob" className='error'>{this.state.dobInvalid}</span>

              </div>
              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align"
                  htmlFor="experience"> Experience <small className="text-muted">(in years)</small> :</label>
                <input type="text" name="experience" placeholder="Enter experience" value={this.state.experience}
                  onChange={this.validateExperience} onKeyDown={this.allowNumbersForExperience} maxLength="5" className="col-form-label col-md-7 col-sm-7" id="experience" autoComplete="off"/>
                  <span id="experience" className='error'>{this.state.experienceInvalid}</span>
              </div>
              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align" htmlFor="user_type">User Type : </label>

                <select id="user_type" className="user_type popupSelect"  disabled={true}>
                  <option value={this.state.user_type}>{this.state.user_type} </option>
                </select>
              </div>
              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align" htmlFor="user_type">Group : </label>

                <select className="popupSelect" id="userGroup"  onClick={this.onUserGroupChange} >
                                         <option value={this.state.userGroup} >{this.state.userGroup} </option>
                                        {optionsgroup.map(group => <option key={group.id} value={group.id}>{group.text}</option>)}
                                    </select>
              </div>

              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align" htmlFor="department">Department :  </label>
                <select className="popupSelect" id="department" onClick={this.onDepartmentChange} >
                <option value={this.state.department} >{this.state.department} </option>
                  {options.map(department => <option key={department.id} value={department.id}>{department.text}</option>)}
                </select>
              </div>
              <div>
              </div>
              <div className="item form-group">
                <label className="col-form-label col-md-5 col-sm-5 label-align" htmlFor="designation">Designation : </label>

                <select className="popupSelect" name="designation" id="designation" onChange={this.onDesignationChange}>
                <option value={this.state.designation} >{this.state.designation} </option>

                  {optionsdesignation.map(designation => <option key={designation.id} value={designation.id}>{designation.text}</option>)}
                </select>
              </div>

            </ModalBody>
            <ModalFooter>
              <Button onClick={this.UpdateUser.bind(this)} disabled={this.state.buttonDisable} type="submit" className="btn btn-success " > Update </Button>
              <Button onClick={this.toggleCloseupdateModal.bind(this)} type="button" className="btn btn-default" >Cancel</Button>
            </ModalFooter>
          </Modal>

          <td>
            
            <a type="button" onClick={this.toggleDeleteModal.bind(this, users.userId)} data-toggle="tooltip" data-placement="top"
							title="Delete user" className="fa fa-trash fa-2x">  </a></td>
          <Modal isOpen={this.state.modaldeleteisOpen}>
            <ModalHeader>
              Delete User
            </ModalHeader>
            <ModalBody>
              Do you want to Delete this record ?
            </ModalBody>
            <ModalFooter>
              <Button onClick={this.deleteUser.bind(this)} type="submit" className="btn btn-success" >Yes</Button>
              <Button onClick={this.toggleClosedeleteModal.bind(this)} type="button" className="btn btn-default" >No</Button>
            </ModalFooter>
          </Modal>

        </tr>
      );
    });
    return (
      <div>
         
							 {/* <div className="col-md-12 col-sm-12 size">         */}
               
                 <h1 align="center" id='titleAllUser'> User List</h1> 

              {/* </div>  */}
        
      
        <div className="col-md-12 col-sm-12">
        {!this.state.getUserList && <div >
                  <select onChange={this.onChange} className="onSelect" >
                  <option>Filter</option>
                    <option value="viewAllUser">All</option>
                    <option value="viewAllAdmin">Admin</option>
                    <option value="viewAllHumanResource">HumanResource</option>
                    <option value="viewAllInterviewer">Interviewer</option>
                    <option value="viewAllManager">Manager</option>
                  </select> 
                  </div>
        }
        {!this.state.getUserList && <div>
              <h1 align="center" id='title'>{this.state.nullMessage}</h1>
        </div>}
        {this.state.getUserList && <div className="viewAllUserTable">
        <div >
                  <select onChange={this.onChange} className="onSelect" >
                  <option>Filter</option>
                    <option value="viewAllUser">All</option>
                    <option value="viewAllAdmin">Admin</option>
                    <option value="viewAllHumanResource">HumanResource</option>
                    <option value="viewAllInterviewer">Interviewer</option>
                    <option value="viewAllManager">Manager</option>
                  </select> 
                  </div>
                  
          <table className="table" id="viewAllUserTable">
            <thead>
              <tr >

                <th align="center"> Index </th>
                <th align="center"> Username </th>
                <th align="center"> User Type </th>
                <th align="center"> Update User </th>
                <th align="center"> Delete User </th>
                
              </tr>
            </thead>
            <tbody>
              {userRow}
            </tbody>
          </table>
          
        </div>
        }
        </div>
        
  </div>
  
    )
  }
}

export default ViewAllUser