import React, { Component } from 'react'
import Interviewer from '../../api/RestApi/CandidateDataService.js'
import { Button } from 'react-bootstrap';
import axios from 'axios';
import moment from 'moment';
import './ScheduledInterview.css'
import { Link } from 'react-router-dom'

const currentdate = new Date();
const currentYear = currentdate.getFullYear();
const maxdate = new Date(currentdate.setYear(currentdate.getFullYear() + 1));
console.log("Date" + maxdate)

const constDesignation = {
    "Designation": [
        { id: 'Engineer', text: 'Engineer' },
        { id: 'SeniorEngineer', text: 'Senior Engineer' },
        { id: 'TechnicalLead', text: 'Technical Lead' },
        { id: 'TechnicalManager', text: 'Technical Manager' }

    ],
    "HumanResource": [
        { id: 'HumanResource', text: 'HumanResource' },
        { id: 'HRBP', text: 'HRBP' }

    ],

}

const constDepartment = {
    "HumanResourceDepartment": [
        { id: 'HumanResource', text: 'HumanResource' },
    ],
    "AllDepartment": [
        { id: 'HumanResource', text: 'HumanResource' },
        { id: 'Digital', text: 'Digital' },
        { id: 'QA', text: 'QA' },
        { id: 'DFT', text: 'DFT' },
        { id: 'Verification', text: 'Verification' }
    ]

}


class ShowInterviewer extends Component {
    constructor() {
        super();
        this.state = ({
            interviewerDepartment: '',
            designation: [],
            department: [],
            interviewerDesignation: '',
            interviewer: [],
            Name: [],
            candidateId: '',
            candidateName: '',
            round: '',
            interviewDate: '',
            interviewTime: '',
            interviewVenue: '',
            interviewerName: '',
            userId: '',
            interviewerId: '',
            buttonDisable: true,

            displayRound: '',
            checkFlag:0,
            date: "",
            time: "",
            dateTime: "",
            datesRange: "",
            interviewRound: "",

        })

    }
    componentDidMount() {

        Interviewer.getCandidate(this.props.match.params.candidateId).then(response => { this.handleCandidate(response) }).catch(response => { this.handleError(response) })


    }

    checkFields = () => {
        const stateData = Object.assign({}, this.state);
        let keys = Object.keys(stateData);
        let isBtnActive = true;
        let formFields = ["interviewDate", "interviewTime", "interviewVenue", "interviewerDepartment", "interviewerDesignation", "interviewerName"];
        console.log(stateData)
        for (var i = 0; i < formFields.length; i++) {
            if (stateData[formFields[i]] == undefined || stateData[formFields[i]] == null || stateData[formFields[i]] == "") {

                isBtnActive = false;
                console.log("check field", isBtnActive)

                break;
            }
        }
        return isBtnActive;
    }



    handleError = (response) => {
        alert("Candidate Not Found")
        this.props.history.push('/UnScheduledInterview');
    }
    handleCandidate = (response) => {
        if (response.data.data.round === 6) {
            this.state.displayRound = "HR Round"
            this.state.department = constDepartment.HumanResourceDepartment
            this.state.designation = constDesignation.HumanResource
        } else {
            this.state.department = constDepartment.AllDepartment
            this.state.displayRound = response.data.data.round
        }

        this.setState({
            candidateName: response.data.data.candidateName,
            candidateId: response.data.data.candidateId,
            round: response.data.data.round,
            interviewRound: response.data.data.round,
            userId: sessionStorage.getItem("userId")
        })

        console.log(this.state.candidateName)
    }

    onDepartmentChange = (event) => {
        if (event.target.value === "") {
            this.setState({
                buttonDisable: true
            })

        }
        this.setState({
            interviewerDepartment: event.target.value,
            Name: []
        })

        //  if(event.target.value===""){
        //      this.setState({
        //          buttonDisable:true
        //      })
        //  }  
        if (event.target.value === "HumanResource") {
            this.setState({
                designation: constDesignation.HumanResource

            })

        } else {
            this.setState({
                designation: constDesignation.Designation
            })
        }

        Interviewer.retrieveAllInterviewers().then(response => { this.getInterviewerName(response) }).catch(response => { this.handleExcepaction(response) });
        let isSubmitActive = this.checkFields();
        if (isSubmitActive) {

            this.setState({
                buttonDisable: false
            })
        }
        if (event.target.value === "") {
            this.setState({
                buttonDisable: true
            })
        }
    }

    handleExcepaction = (response) => {
        this.setState({
            Name: []
        })
    }
    onDesignationChange = (event) => {

        this.setState({
            Name: []
        })
        this.setState({
            interviewerDesignation: event.target.value
        })
        if (event.target.value === "") {
            this.setState({
                buttonDisable: true
            })
        }
        Interviewer.retrieveAllInterviewers().then(response => { this.getInterviewerName(response) }).catch(response => { this.handleExcepaction(response) });

    }
    getInterviewerName = (response) => {
        console.log(response.status)
        this.setState({
            interviewer: response.data.dataList

        })

        this.state.interviewer.map((interviewer, i) => {

          

            if (interviewer.department === this.state.interviewerDepartment && interviewer.designation === this.state.interviewerDesignation && this.state.interviewTime >= interviewer.availableTimeFrom && this.state.interviewTime <= interviewer.availableTimeTo && this.state.interviewDate === interviewer.availableDate) {
              this.state.checkFlag=this.state.checkFlag+1;
                this.setState({
                    Name: this.state.Name.concat([{ id: interviewer.userDetailsId, text: interviewer.name }]),
                 
                    // Name:this.state.Name.concat(interviewer.interviewer_name)
                })
                console.log("Check Flag is"+this.state.checkFlag)

            }  
            // if(this.state.checkFlag<=0){
            //     this.state({
            //         Name:[]
            //     })
            // }
        })

        let isSubmitActive = this.checkFields();
        if (isSubmitActive) {

            this.setState({
                buttonDisable: false
            })
        }


    }

    onInterviewerChange = (event) => {

        this.setState({
            interviewerId: event.target.value
        })
        this.state.interviewerName = event.target.value
        console.log("The Name is " + this.state.interviewerName)

        let isSubmitActive = this.checkFields();
        if (isSubmitActive) {

            this.setState({
                buttonDisable: false
            })
        }
        if (event.target.value === "") {
            this.setState({
                buttonDisable: true
            })
        }
    }

    handleInterviewDate = (event) => {
        
        this.setState({
            interviewDate: event.target.value,
            Name:[],
            interviewerName:''
        })
        this.state.interviewDate = event.target.value
        console.log(this.state.interviewDate)
        let isSubmitActive = this.checkFields();
        if (isSubmitActive) {

            this.setState({
                buttonDisable: false
            })
        }


        Interviewer.retrieveAllInterviewers().then(response => { this.getInterviewerName(response) }).catch(response => { this.handleExcepaction(response) });
    }

    handleInterviewTime = (event) => {
        this.setState({
            interviewTime: event.target.value,
            Name:[],
            interviewerName:''

        })
        this.state.interviewTime = event.target.value
        let isSubmitActive = this.checkFields();
        if (isSubmitActive) {

            this.setState({
                buttonDisable: false
            })
        }

        Interviewer.retrieveAllInterviewers().then(response => { this.getInterviewerName(response) }).catch(response => { this.handleExcepaction(response) });
    }
    handleVenueChange = (event) => {
        this.setState({
            interviewVenue: event.target.value
        })
        this.state.interviewVenue = event.target.value
        let isSubmitActive = this.checkFields();
        if (isSubmitActive) {

            this.setState({
                buttonDisable: false
            })
        }


        if (event.target.value === "") {
            this.setState({
                buttonDisable: true
            })
        }
    }


    handleSubmit = () => {
        axios.post('http://localhost:8080/scheduleinterview', this.state).then(response => { this.handleScheduleInteviewSuccess(response) }).catch(response => { this.handleScheduleInteviewError(response) })
    }
    handleScheduleInteviewSuccess = (response) => {
        alert("Interview Schedule Successfully")
        window.history.back()
    }

    handleScheduleInteviewError = (error,errorinfo) => {

        console.log("==========" + error)
        console.log(error.response.status)
        if (error.response.status === 404) {
            alert("Enter valid data.")
            window.history.back()
        }
        if (error.response.status === 422) {
            alert("Soemthing went wrong.")
            window.history.back()
            // this.setState({

            //     errorMessage: `All feilds are mandatory.`,
          
            //     isError: true,
            // })
        }
        if (error.response.status = 400) {
            alert("Candidate not found.")
            window.history.back()
        }
        // alert("Interview Is  Not 

    }

    handleChange = (event, { name, value }) => {
        if (this.state.hasOwnProperty(name)) {
            this.setState({ [name]: value });
        }
    };

    render() {
        return (
            <>
            
                <div className="container" id="scheduleInterviewForm">
                <h2 className="heading" align="center"><b>Schedule Interview</b></h2><br />
                    <div>
                        <div className="row ">
                            <div className="col-md-6">
                                User Name <input type="text" readOnly value={this.state.candidateName} />
                            </div>



                            <div className="col-md-6">
                                Round <input type="text" readOnly value={this.state.displayRound} />
                            </div>

                        </div>
                        <div className="row ">
                            <div className="col-md-6">
                                <label for="dob-label">Interview Date</label><span className="error">*</span>
                                <input type="date" name="interviewDate" className="interviewDate" onChange={this.handleInterviewDate} min={moment().format("YYYY-MM-DD")}
                                    value={this.state.interviewDate} />
                                {/* max={moment().format("YYYY-MM-DD")+365} min={moment().format("YYYY-MM-DD")} */}
                            </div>



                            <div className="col-md-6">
                                <label for="dob-label">Interview Time</label><span className="error">*</span>
                                <input type="time" name="time" className="interviewTime" onChange={this.handleInterviewTime} value={this.state.interviewTime} />

                            </div>
                        </div>
                        <div className="row ">
                            <div className="col-md-6">
                                <label htmlFor="interviewVenue">Venue <span className="error">*</span></label>
                                <div>
                                    <select id="interviewVenue" className="interviewVenue" onChange={this.handleVenueChange}>

                                        <option value=""> Select Venue </option>

                                        <option id="MCG">MCG</option>
                                        <option id="EdenGarden">EdenGarden</option>
                                        <option id="Tauras">Tauras</option>
                                        <option id="Orion">Orion</option>

                                    </select>
                                </div>
                            </div>


                            <div className="col-md-6">
                                <label htmlFor="interviewerDepartment">Interviewer Department <span className="error">*</span></label>
                                <div>
                                    <select id="interviewerDepartment" className="interviewerDepartment" onChange={this.onDepartmentChange}>

                                        <option value="">Select Interviewer Department </option>
                                        {this.state.department.map(department => <option key={department.id} value={department.text}>{department.text}</option>)}


                                    </select>
                                </div> </div>

                            <div className="col-md-6">
                                <label htmlFor="interviewerDesignation">Interviewer Designation <span className="error">*</span></label>
                                <div>
                                    <select id="interviewerDesignation" className="interviewerDesignation" onChange={this.onDesignationChange}>
                                        <option value="">Select Interviewer Designation </option>
                                        {this.state.designation.map(designation => <option key={designation.id} value={designation.text}>{designation.text}</option>)}

                                    </select>
                                </div>
                            </div>



                            <div className="col-md-6">
                                <label htmlFor="interviewerJobDesignation">Interviewer Name <span className="error">*</span></label>
                                <div>

                                    <select id="interviewerName" className="interviewerName" onChange={this.onInterviewerChange}>
                                        <option value="">Select Interviewer Name </option>
                                        {this.state.Name.map(Name => <option key={Name.id} value={Name.id}>{Name.text}</option>)}

                                    </select>
                                </div>
                            </div>
                            <div className="SubmitDiv" align="center">
                                <Button onFocus={this.checkFields} disabled={this.state.buttonDisable} onClick={this.handleSubmit} variant="primary"> Submit</Button>
                                &nbsp; &nbsp; <Link to="/UnScheduledInterview" className="link"><Button variant="secondary">Back </Button></Link>
                            </div>


                        </div>

                    </div>
                </div>


            </>

        )
    }
}
export default ShowInterviewer;