import axios from 'axios'

class UserDataService{

    addUser(data){
        return axios.post('http://localhost:8080/adduser',data);
    }

    retrieveAllUsers(){
        return axios.get('http://localhost:8080/viewalluser');
    }

    deleteUser(userid){
        return axios.delete(`http://localhost:8080/deleteuserdetails/${userid}`)
    }

    updateUser(userid,data){
        return axios.put(`http://localhost:8080/updateuserdetails/${userid}`,data)
    }

     retrieveUserDetails(userid){
        return axios.get(`http://localhost:8080/viewuserdetails/${userid}`)
    }
    
    updateAdminProfile(userid,data){
        return axios.put(`http://localhost:8080/updateadminprofile/${userid}`,data)
    }

    retrieveAllInterviewer(){
        return axios.get('http://localhost:8080/viewAllInterviewer')
    }
    retrieveAllManager(){
        return axios.get('http://localhost:8080/viewAllManager')
    }

    retrieveAllAdmin(){
        return axios.get('http://localhost:8080/viewAllAdmin')
    }

    retrieveAllHumanResource(){
        return axios.get('http://localhost:8080/viewAllHumanResource')
    }


    addUserDetails(data){
        return axios.post('http://localhost:8080/adduserdetails',data);
    }
}
export default new UserDataService()