import React from "react"
import { Component } from "react"
import InterviewerDataService from '../../api/RestApi/InterviewerDataService.js'
import { Button,Modal,ModalBody,ModalFooter,ModalHeader } from 'reactstrap'
import Moment from 'react-moment';
import CandidateDataService from "../../api/RestApi/CandidateDataService.js";
import './ViewAllFeedback.css'
class pendingInterviewApproval extends Component {
  constructor() {
    super();
    this.state = {
      candidates: [],
      candidateId: '',
      getCandidateList: false,
      errorMesssge: '',
      modalAcceptRejectisOpen:'',
      userId: sessionStorage.getItem("userId")
    }
  }

  componentDidMount() {
    InterviewerDataService.retrievePenddingInterviewApproval(this.state.userId).then(response => {

      if(response.data.dataList.length!=0){
      this.setState({
        candidates: response.data.dataList,
        getCandidateList: true
        
      })
     
    }else{
      this.setState({
        getCandidateList: false,
        errorMesssge: "No Any Pending Interview Approval Found.",
      })
    }
    }).catch(response => {
      this.setState({
        getCandidateList: false,
        errorMesssge: "Something Went Wrong.",
      })
    })

  }

  viewResume = (candidateId) => {
    CandidateDataService.getResume(candidateId).then().catch(error => console.error('Error:', error));
  }

  toggleAcceptRejectModal=(candidateId,interviewId)=> {
    this.setState({  candidateId: candidateId, interviewId: interviewId,modalAcceptRejectisOpen: !this.state.modalAcceptRejectisOpen})
    
  }
  toggleCloseRoundFeedbackModal = () => {

    this.setState({ modalAcceptRejectisOpen: !this.state.modalAcceptRejectisOpen })
  }

  AcceptInterview = () => {
    InterviewerDataService.acceptInterview(this.state.interviewId).then(response => this.acceptedInterview(response))
    .catch(response => this.componentDidCatchAccept(response))
  }

   acceptedInterview = (response) => {
      this.componentDidMount();
   }

   componentDidCatchAccept = (error) => {
    if(error.response.status === 404){  
      alert("Record not found.");
    }

    if(error.response.status === 401){  
      alert(" Unauthorized user.");
    }
   }

  RejectInterview = () => {
    InterviewerDataService.rejectInterview(this.state.interviewId,this.state.candidateId).then(response => this.rejectedInterview(response))
    .catch(response => this.componentDidCatchReject(response))
  }

   rejectedInterview = (response) => {
    this.componentDidMount();
   }

   componentDidCatchReject = (error) => {
    if(error.response.status === 404){  
      alert("Record not found.");
    }

    if(error.response.status === 401){  
      alert(" Unauthorized user.");
    }
   }

   toggleCloseAcceptRejectModal  = () => {

    this.setState({ modalAcceptRejectisOpen: !this.state.modalAcceptRejectisOpen })
  }


  render() {

    let userRow = this.state.candidates.map((candidates, i) => {

      return (
        <tr>
          <td>{candidates.candidateName}</td>
          <td x-ms-format-detection="none" >{candidates.contactNumber}</td>
          <td>{candidates.qualification}</td>
          <td><Moment format="DD/MM/YYYY" date={candidates.dateOfBirth}></Moment></td>
          <td>{candidates.emailId}</td>
          <td align="center">{candidates.experience}</td>
          <td>{candidates.gender}</td>
          <td><Moment format="DD/MM/YYYY" date={candidates.interviewDate}></Moment></td>
          <td>{candidates.name}</td>
          <td><a href={'http://localhost:8080/showresume/' + candidates.candidateId + ''} target="_blank" data-toggle="tooltip" title="View Resume"><i class="fa fa-file fa-2x"></i></a>
        </td>
         <td> &nbsp;<a type="button" onClick={this.toggleAcceptRejectModal.bind(this, candidates.candidateId,candidates.interviewId)} data-toggle="tooltip" data-placement="top"
							title="Accept/Reject Interview Request" className="fa fa-clock-o fa-2x">  </a></td>
          
          <Modal isOpen={this.state.modalAcceptRejectisOpen}>
            <ModalHeader toggle={this.toggleCloseAcceptRejectModal.bind(this)}>
              Accept/Reject Interview
            </ModalHeader>
            <ModalBody>
              Do you want to accept Interview Request ?
            </ModalBody>
            <ModalFooter>
              <Button onClick={this.AcceptInterview.bind(this)} type="submit" className="btn btn-success" > Accept </Button>
              <Button onClick={this.RejectInterview.bind(this)} type="button" className="btn btn-danger" > Reject </Button>
            </ModalFooter>
          </Modal>
        </tr>
      );
    });

    return (
      <div>

        <h1 align="center" id='titleviewallfeedback'>List Of Pending Interview Approval</h1>
        <div className="col-md-12 col-sm-12">

          {!this.state.getCandidateList && <div>
            <h1 align="center" id='title1'>{this.state.errorMesssge}</h1>

          </div>}

          {this.state.getCandidateList && <div className="pendingInterviewApproval">
            <table className="table" id="pendingInterviewApproval">
              <thead>
                <tr>
                  <th>Candidate Name </th>
                  <th align="center">Contact Number </th>
                  <th align="center">Qualification </th>
                  <th align="center">DOB </th>
                  <th align="center">EmailId </th>
                  <th align="center">Experience (In Years)</th>
                  <th align="center">Gender </th>
                  <th align="center">Interview Date</th>
                  <th align="center">Interviewer Name</th>
                  <th align="center">Resume</th>
                  <th align="center">Action</th>

                </tr>
              </thead>
              <tbody>
                {userRow}
              </tbody>
            </table>
          </div>}
        </div>

      </div>
    )
  }
}
export default pendingInterviewApproval;