import React,{Component} from 'react'
import AuthUser from './AuthUser.js'

class Welcome  extends Component{
    render(){
        const isLogin=AuthUser.checkUser()
        const validUser=AuthUser.isValidUser()
        return(
            <div >
               {isLogin && validUser && <div>
             <h1 > Welcome page.. </h1>
           
             </div>
             
             
             }
            </div>
        )
    }
}
export default Welcome