import React from "react"
import { Component } from "react"
import InterviewerDataService from '../../api/RestApi/InterviewerDataService.js'
import CandidateDataService from '../../api/RestApi/CandidateDataService.js'
import axios from 'axios'
import './HireCandidate.css'

import { BrowserRouter as Router, Route, Switch, Link } from 'react-router-dom';
import { Button } from 'reactstrap'
import Moment from 'react-moment';

class HiredCandidate extends Component {
  constructor() {
    super();
    this.state = {
      candidates: [],
      candidateId: '',
      getCandidateList: false,
      errorMesssge: '',
      userId: sessionStorage.getItem("userId")

    }
  }

  componentDidMount() {
    InterviewerDataService.getHiredCandidateList(this.state.userId).then(response => {
      if(response.data.dataList.length!=0){
      this.setState({
        candidates: response.data.dataList,
        getCandidateList: true
      })
    }else{
      this.setState({
        getCandidateList: false,
        errorMesssge: "There Is No Hired Candidates.",
      })
    }

    }).catch(response => {
      this.setState({
        getCandidateList: false,
        errorMesssge: "Something Went Wrong.",
      })
    })

  }

  viewResume = (candidateId) => {
    CandidateDataService.getResume(candidateId).then().catch(error => console.error('Error:', error));


  }

  render() {

    let userRow = this.state.candidates.map((candidates, i) => {

      return (

        <tr>
          <td></td>
          <td>{candidates.candidateName}</td>
          {/* <td>{candidates.address}</td> */}
          <td>{candidates.qualification}</td>
          <td align="center">{candidates.experience}</td>
          <td>{candidates.candidateDepartment}</td>
          <td>{candidates.candidateDesignation}</td>
          <td>{candidates.interviewResult}</td>
          <td><a href={'http://localhost:8080/showresume/' + candidates.candidateId + ''} target="_blank" data-toggle="tooltip" title="View Resume"><i class="fa fa-file fa-2x"></i></a></td>


        </tr>
      );
    });

    return (
      <div>

        <h1 align="center" id='titleHeading'>List Of  Hired Candidates </h1>
        <div className="col-md-12 col-sm-12">

          {!this.state.getCandidateList && <div>

            <h1 align="center" id='title1'>{this.state.errorMesssge}</h1>


          </div>}

          {this.state.getCandidateList &&
            <div className="HireCandidateTable">
              <br></br>
              <br></br>
              <table className="table" id="HireCandidateTable">
                <thead>

                  <tr>
                  <th align="center">Index </th>
                    <th align="center">Candidate Name </th>
                    {/* <th>Address </th> */}
                    
                    <th align="center">Qualification </th>
                    <th align="center">Experience (In Years)</th>
                    <th align="center">Candidate Department</th>
                    <th align="center">Candidate Designation</th>
                    <th align="center">Status</th>
                    <th align="center">Resume</th>

                  </tr>
                </thead>
                <tbody>
                  {userRow}
                </tbody>
              </table>
            </div>
          }
        </div>

      </div>
    )
  }
}
export default HiredCandidate;