import React from "react"
import { Component } from "react"
import './HumanResourseDashbord.css'
import { Container, Row, Col } from 'react-grid';
import ReactDOM from 'react-dom';
import { gridData } from '../HumanResourseComponent/appData';
import { Button } from 'react-bootstrap';
import Ripples from 'react-ripples'
// import { savePDF } from 'react-pdf';



class HumanResourseDashbord extends Component{
    render() {
        return (
          <div className="bootstrap-wrapper">
            <div className="app-container container">
              <div className="row">
                <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6 col-xl-6">
                  <h1>Sales | Q4 2018</h1>
                </div>
                <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 buttons-right">
                <Ripples>
  <Button primary={true}>Share</Button>
  <Button>Export to PDF</Button>
</Ripples>
</div>

              </div>
              <div className="row">
                <div className="col-xs-3 col-sm-3 col-md-3 col-lg-3 col-xl-3">
                  <h4>Panel Bar Container</h4>
                </div>
                <div className="col-xs-9 col-sm-9 col-md-9 col-lg-9 col-xl-9">
                  <div className="row">
                    <div className="col-xs-6 col-sm-6 col-md-4 col-lg-4 col-xl-4">
                      <h4>Donut Chart Container</h4>
                    </div>
                    <div className="col-xs-6 col-sm-6 col-md-2 col-lg-2 col-xl-2">
                      <div className="percentage-container">
                        <span className="percentage-number">94</span>
                        <span className="percentage-sign">%</span>
                        <p>CUSTOMER SATISFACTION</p>
                      </div>
                      <div className="percentage-container">
                        <span className="percentage-number">89</span>
                        <span className="percentage-sign">%</span>
                        <p>TARGET SALES</p>
                      </div>
                    </div>
                    <div className="col-xs-12 col-sm-12 col-md-6 col-lg-6 col-xl-6">
                      <h4>Bar Chart Container</h4>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12 col-xl-12">
                      <h4>Grid Container</h4>
                      {/* <Container  style={{ height: '300px' }} data={gridData}>
      <Col field="ProductID" title="ID" width="40px" />
      <Col field="ProductName" title="Name" width="160px" />
      <Col field="Category.CategoryName" title="Category Name" width="80px" />
      <Col field="UnitPrice" title="Price" width="80px" />
      <Col field="UnitsInStock" title="Stock" width="90px" />
      <Col field="Discontinued" width="130px"/>
    </Container > */}
                    </div>
                  </div>
                </div>
              </div>
              <h4 style={{ display: 'none' }}>Dialog Shown/Hidden with Logic</h4>
            </div>
          </div>
        );
      }
}

export default HumanResourseDashbord;