import React, { Component } from 'react'
import Header from './Header.jsx'
import { BrowserRouter as Router, Route, Switch, Link } from 'react-router-dom';
import Footer from './Footer.jsx'
import Welcome from './Welcome.jsx'
import SideMenu from './SideMenu'
import Login from './login.jsx'
import AuthUser from './AuthUser.js'
import AddCandidate from '../../component/HumanResourseComponent/AddCandidate.jsx'
import ViewAllCandidates from '../HumanResourseComponent/viewAllCandidates.jsx'
import GiveFeedback from '../interviewerComponent/GiveFeedback.jsx'
import ViewAllUser from '../adminComponent/ViewAllUser.jsx'
import ViewAllAdmin from '../adminComponent/ViewAllAdmin.jsx'
import ViewAllInterviewer from '../adminComponent/ViewAllInterviewer.jsx'
import ViewAllManager from '../adminComponent/ViewAllManager.jsx'

import ViewAllFeedback from '../interviewerComponent/ViewAllFeedback.jsx'
import ViewAllHumanResource from '../adminComponent/ViewAllHumanResource.jsx'
import Forgotpassword from '../../component/logincomponent/forgotpassword.jsx'
import Changepassword from '../../component/logincomponent/restpassword.jsx'
import InValidURL from '../../component/logincomponent/InValidURL.jsx'
import CollapseMenu from './CollapseMenu.jsx'
import ShowInterviewer from '../HumanResourseComponent/ShowInterviewer.jsx'
import UnScheduledInterview from '../HumanResourseComponent/UnscheduledInterview.jsx'
import PenddingInterviewApproval from '../HumanResourseComponent/PenddingInterviewApproval.jsx'
import pendingInterviewApproval from '../interviewerComponent/PendingInterviewApproval.jsx'
import ScheduledInterview from '../HumanResourseComponent/ScheduledInterview.jsx'
import AddUsernew from '../adminComponent/AddUsernew.jsx'
import AdminDashboard from '../adminComponent/AdminDashboard.jsx'
import HiredCandidate from '../interviewerComponent/HiredCandidate.jsx'
import RejectedCandidatelist from '../interviewerComponent/RejectedCandidate.jsx'


import GenerateFeedbackforInterviewer from '../interviewerComponent/GenerateFeedbackforInterviewer.jsx'
import GenerateFeedbackforHumanResourse from '../interviewerComponent/FeedbackFormHumanResourse.jsx'
import HireCandidate from '../HumanResourseComponent/HiredCandidate.jsx'
import RejectedCandidate from '../HumanResourseComponent/RejectedCandidate.jsx'
import RescheuleInterview from '../HumanResourseComponent/RescheduleInterview.jsx'
import Dashboard from '../HumanResourseComponent/Dashboard.jsx'
import FeedbackOfToday from '../interviewerComponent/FeedbackOfToday.jsx';
import FeedbackOfYesterday from '../interviewerComponent/FeedbackOfYesterday.jsx';
import FeedbackOfLastweek from '../interviewerComponent/FeedbackOfLastweek.jsx';
import InterviewOfToday from '../interviewerComponent/InterviewOfToday.jsx';
import FeedbackCandidateHR from '../interviewerComponent/FeedbackCandidateHR.jsx'
import ScheduleInterview from '../interviewerComponent/ScheduledInterview';
import FeedbackFilterHorizontalChart from '../charts/FeedbackFilterHorizontalChart.jsx'
import InterviewerFilterDonutChart from '../charts/InterviewerFilterDonutChart.jsx'
import InterviewerDashboard from '../interviewerComponent/InterviewerDashboard.jsx'
import DashboardHR from '../HumanResourseComponent/MainDashboardHR.jsx'
import PieCharts from '../charts/PieChart.jsx'
import LineChart from '../charts/InterviewStatusChart.jsx'
import HumanResourseDashbord  from '../HumanResourseComponent/HumanResourseDashbord.jsx'
import JobPosting from '../managerComponent/JobPosting.jsx'
import ShowJobPosting from '../managerComponent/ShowJobPosting.jsx'

import CandidateQualificationChart from '../charts/CandidateQualificationChart.jsx'

import DynamicAddCandidate from '../HumanResourseComponent/DynamicAddCandidate.jsx'
import AcceptedCandidate from '../interviewerComponent/AcceptedCandidate.jsx';

class ImsApp extends Component {
   constructor(){
       super();
       }
    render() {
  
        return (
            
            <div className="div-body">
                <Router>
                    <div class="row layout-row padding-0">
                   
                    {/* <div class="col-md-2 padding-0 sidemenu"> */}
                        {/* <div class="icon-box">
                            <img src="logo192.png" alt="..." class="site-icon"/>
                        </div> */}
                        <div>
                            < SideMenu ></SideMenu>
                            {/* <CollapseMenu></CollapseMenu> */}
                        </div> 
                    {/* </div> */}
                     
                    <Route path="/" exact component={Login} ></Route>

                        <div class="padding-0 main-panel">
                        <Header></Header>
          {/* <div className="login">  */}

                        {/* <div className="login">  */}
                                
                  {/* </div> */}

                  {/* <div className="content innercontent-style"> 
                        <Route path="/forgotpassword" exact component={Forgotpassword}></Route>
                        <Route path="/restpassword/:userId" exact component={Changepassword}></Route>
                        <Route path="/InValidURL"  component={InValidURL}></Route>
                      {/* </div> */}
                            <Route path="/dashboard" component={Dashboard}></Route>
                            <div class="content">
                                <Switch>
                                    
                                  <div className="component"> 
                                    {/* <Route path="/" exact component={Login} ></Route> */}
                                
                                    <Route path="/welcome"  component={Welcome}></Route>
                                    <Route path="/AddCandidate"  component={AddCandidate}></Route>
                                    <Route path="/addUser"  component={AddUsernew}></Route>
                                    <Route path="/viewAllUser"  component={ViewAllUser}></Route>
                                    {/* <Route path="/viewAllUserChart"  component={ViewAllUserChart}></Route> */}
                                    <Route path="/adminDashboard"  component={AdminDashboard}></Route>
                                    <Route path="/candidateQualificationChart"  component={CandidateQualificationChart}></Route>


                                    <Route path="/viewAllAdmin"  component={ViewAllAdmin}></Route>
                                    <Route path="/viewAllInterviewer"  component={ViewAllInterviewer}></Route>
                                    <Route path="/viewAllManager"  component={ViewAllManager}></Route>

                                    <Route path="/viewAllHumanResource"  component={ViewAllHumanResource}></Route>
                                    <Route path="/FeedbackOfToday"  component={FeedbackOfToday}></Route>
                                    <Route path="/FeedbackOfYesterday"  component={FeedbackOfYesterday}></Route>
                                    <Route path="/FeedbackOfLastweek"  component={FeedbackOfLastweek}></Route>

                                    

                                    <Route path="/ShowCandidate"  component={ViewAllCandidates}></Route>
                                    <Route path="/ShowInterviewer/:candidateId" component={ShowInterviewer}></Route>
                                    <Route path="/GiveFeedback/:userId" component={GiveFeedback}></Route>
                                    <Route path="/ViewAllFeedback" component={ViewAllFeedback}></Route>                                    
                                    <Route path="/UnScheduledInterview" component={UnScheduledInterview}></Route>
                                    <Route path="/PenddingInterviewApproval" component={PenddingInterviewApproval}></Route>
                                    <Route path="/pendingInterviewApprovalList" component={pendingInterviewApproval}></Route>
                                    <Route path="/Forgotpassword" component={Forgotpassword}></Route>
                                    <Route path="/restpassword/:userId" component={Changepassword}></Route>
                                    <Route path="/InValidURL"  component={InValidURL}></Route>
                                    <Route path="/ScheduledInterview" component={ScheduledInterview}></Route>
                                    <Route path="/Feedbackinterviewer/:candidateId" component={GenerateFeedbackforInterviewer}></Route>                                    
                                    <Route path="/RejectedCandidate" component={RejectedCandidate}></Route>
                                    <Route path="/Feedbackhumanresourse/:candidateId" component={GenerateFeedbackforHumanResourse}></Route>
                                    <Route path="/Feedbackforhrcandidate/:candidateId" component={FeedbackCandidateHR}></Route>
                                    <Route path="/HireCandidate" component={HireCandidate}></Route>
                                    <Route path="/RescheuleInterview/:candidateId" component={RescheuleInterview}></Route>
                                    <Route path="/scheduled/:userId" component={ScheduleInterview}></Route>
                                    {/* <Route path="/Feedbackhumanresourse/:candidateId" component={GenerateFeedbackforHumanResourse}></Route> */}
                                    <Route path="/FeedbackFilterHorizontalChart" component={FeedbackFilterHorizontalChart}></Route>
                                    <Route path="/InterviewerFilterDonutChart" component={InterviewerFilterDonutChart}></Route>
                                    <Route path="/Piechart" component={PieCharts}></Route>
                                    <Route path="/Linechart" component={LineChart}></Route>
                                    <Route path="/Dashboardhr" component={DashboardHR}></Route>
                                    <Route path="/InterviewOfToday"  component={InterviewOfToday}></Route>
                                    <Route path="/InterviewerDashboard"  component={InterviewerDashboard}></Route>
                                    <Route path="/foradd" component={DynamicAddCandidate}></Route>

                                    <Route path="/HiredCandidate"  component={HiredCandidate}></Route>

                                    <Route path="/AcceptedCandidate"  component={AcceptedCandidate}></Route>

                                    <Route path="/RejectedCandidateList"  component={RejectedCandidatelist}></Route>


{/* 
									 {/* <Route path="/chart" component={Charts}></Route>
                                    <Route path="/chartUsingChart" component={chartUsingChart}></Route> */}
                                   
                                    
                                    <Route path="/HumanResourseDashbord" component={HumanResourseDashbord}></Route>
                                    <Route path="/JobPosting" component={JobPosting}></Route>
                                    <Route path="/ShowJobPosting" component={ShowJobPosting}></Route>
                                    </div> 
                                   
                               
                                </Switch>
                            </div>
                            <div class="col-md-12 padding-0 fixed-bottom">
                                <Footer></Footer>
                            </div>
                        </div>
                    </div>
                </Router>
            </div>
        )
    }
}



export default ImsApp

