import React from 'react';
import { Doughnut } from 'react-chartjs-2';
import CandidateDataService from '../../api/RestApi/CandidateDataService.js'



export default class chartUsingChart extends React.Component {




  constructor() {
    super();
    this.state = {
      candidates: [],
      candidateId: '',
      getCandidateList: false,
      errorMesssge: '',
      chart: '',
      i: '',
      count: '',
      pendingCandidateCount: 0,
      hireCandidateCount: 0,
      rejectCandidateCount: 0,
    }
  }


  componentDidMount() {
    CandidateDataService.getListOfCandidates().then(response => {
      if (response.data.dataList.length != 0) {
        this.setState({
          candidates: response.data.dataList,
          getCandidateList: true,


        })



      } else {
        this.setState({
          getCandidateList: false,
          errorMesssge: "Record Not Found.",
        })
      }
    }).catch(response => {
      this.setState({
        getCandidateList: false,
        errorMesssge: "Something Went Wrong.",
      })
    })

  }





  render() {

    let userRow = this.state.candidates.map((candidates, i) => {
      //  console.log(candidates.interviewResult)

      var countNAN = 0;
      var countAccepted = 0;


      // console.log(candidates.interviewResult)
      if (candidates.interviewResult === "NAN") {
        this.state.pendingCandidateCount = this.state.pendingCandidateCount + 1;
      } else if (candidates.interviewResult === "Hire") {
        this.state.hireCandidateCount = this.state.hireCandidateCount + 1;
      } else if (candidates.interviewResult === "Reject") {
        this.state.rejectCandidateCount = this.state.rejectCandidateCount + 1;
      }





      return (

        this.state.chart = {
          labels: ['In-Process', 'Hired', 'Rejected'],
          datasets: [
            {
              // label: ' ',
              backgroundColor: [
                '#ADD8E6',
                '#C9DE00',
                '#fed8b1',
                '#0000ff',

              ],
              hoverBackgroundColor: [
                '#501800',
                '#4B5000',
                '#175000',

              ],
              data: [this.state.pendingCandidateCount, this.state.hireCandidateCount, this.state.rejectCandidateCount],

            }
          ]
        }



      );
    });


    return (
      <div>
         {this.state.getCandidateList &&<Doughnut
          data={this.state.chart}
          width={"02%"}
          height={"1%"}
          options={{
            title: {
              display: true,
              text: 'Result Of Candidate',
              fontSize: 20
            },
            legend: {
              display: true,
              position: 'right'
            },

          }}
        />} 

{!this.state.getCandidateList && <div>
  <h1>No Data</h1>
  </div>}
      </div>
    );
  }
}

