import React, { Component } from 'react'
import axios from 'axios'
import { Button } from 'react-bootstrap';
import InterviewerDataService from '../../api/RestApi/InterviewerDataService';
import Interviewer from '../../api/RestApi/CandidateDataService.js'
import Listofscheduledinterview from '../interviewerComponent/ListScheduleInterview.jsx'
import "./HrFeedback.css"


class GenerateFeedbackforHumanResourse extends Component {

    constructor() {
        super();
        this.state = {
            candidateId:'',
            candidateName:'',
      round:'',
      candidateDesignation:'',
      jobRole:'',
      interviewerName:'',
      experience:'',
      dateOfInterview:'',
     // technicalSkill:'',
     educationSkill:'',
     organizationalSkill:'',
      nextRound:'',
      interviewResult:'',
      review:'',
      overallRating:'',
      communicationSkill:'',
      interviewerId:'',
      interviewId:'',
      buttonDisable: true,
      radiobuttonDisable:true,
      nextRoundRequired:'',
      hrLeaderAbility:'',
      space:''
        }
       
    }

    componentDidMount(){

        Interviewer.getCandidate(this.props.match.params.candidateId).then(response=>{this.handleCandidate(response)}).catch(response=>{this.handleError(response)})
        
    
    }


    handleCandidate=(response)=>{
        this.setState({
            candidateName:response.data.data.candidateName,
            candidate:response.data.data.candidateId,
             round:response.data.data.round,
             jobRole:response.data.data.candidateDesignation,
             experience:response.data.data.experience,
             dateOfInterview:response.data.data.interviewDate,
             interviewer:response.data.data.userDetailsId,
             interviewerName: response.data.data.interviewerName,
             interview: response.data.data.interviewId,
               interviewer:response.data.data.interviewerId

        })

        
    
        console.log("candidatename"+this.state.candidateName)
        console.log("interviewerId"+this.state.interviewer)
        console.log("interviewId"+this.state.interview)
    }

    handleError=(response)=>{
        
        
        alert("Some thing went wrong")
        this.props.history.push('/Listofscheduledinterview');
    }

    reviewHandle =(event) =>{
        this.setState({
            review:event.target.value
        })
        this.state.review = event.target.value
        let isSubmitActive = this.checkFields();
        
        if(isSubmitActive){
            console.log("abcd",event)
            this.setState({
                buttonDisable:false
            })
        }else if(event.target.value === ""){
            this.setState({
                buttonDisable:true
            })
        }

    }

    handlehrLeaderAbility =(event) =>{
        this.setState({
            hrLeaderAbility:event.target.value
        })
        this.state.hrLeaderAbility = event.target.value
        let isSubmitActive = this.checkFields();
        console.log(this.state.hrLeaderAbility)
        if(isSubmitActive){
           
            this.setState({
                buttonDisable:false
            })
        }else if(event.target.value === ""){
            this.setState({
                buttonDisable:true
            })
        }
    }

    handleEducationSkill =(event) =>{
        this.setState({
            educationSkill:event.target.value
        })
        this.state.educationSkill = event.target.value
        let isSubmitActive = this.checkFields();
        
        if(isSubmitActive){
            this.setState({
                buttonDisable:false
            })
        }else if(event.target.value === ""){
            this.setState({
                buttonDisable:true
            })
        }
    }

    handleorganizationalSkill =(event) =>{
        this.setState({
            organizationalSkill:event.target.value
        })
        this.state.organizationalSkill = event.target.value
        let isSubmitActive = this.checkFields();
        
        if(isSubmitActive){
            this.setState({
                buttonDisable:false
            })
        }else if(event.target.value === ""){
            this.setState({
                buttonDisable:true
            })
        }
    }

    handlecommunicationalSkill =(event) =>{
        this.setState({
            communicationSkill:event.target.value
        })
        this.state.communicationSkill = event.target.value
        let isSubmitActive = this.checkFields();
        
        if(isSubmitActive){
            this.setState({
                buttonDisable:false
            })
        }else if(event.target.value === ""){
            this.setState({
                buttonDisable:true
            })
        }
    }

    handleRating =(event) =>{
        this.setState({
            overallRating:event.target.value
        })
        this.state.overallRating = event.target.value
        let isSubmitActive = this.checkFields();
        
        if(isSubmitActive){
            this.setState({
                buttonDisable:false
            })
        }else if(event.target.value === ""){
            this.setState({
                buttonDisable:true
            })
        }

    }

    handleresult =(event) =>{
        this.setState({
            interviewResult:event.target.value
        })
        this.state.interviewResult = event.target.value
       
        let isSubmitActive = this.checkFields();

        if(isSubmitActive){
            this.setState({
                buttonDisable:false
            })
        }else if(event.target.value === ""){
            this.setState({
                buttonDisable:true
            })
        }
    }
    handleNextRound =(event) =>{
        this.setState({
            nextRound:event.target.value
        })
        this.state.nextRound = event.target.value
        let isSubmitActive = this.checkFields();
        
        if(isSubmitActive){
            this.setState({
                buttonDisable:false
            })
        }else if(event.target.value === " "){
            this.setState({
                buttonDisable:true
            })
        }
    }

   

   

    loginButton = () => {
          axios.post('http://localhost:8080/humanresourcefeedback' , this.state).then(response => this.feedbackAdded(response)).catch(response => this.errorFeedback(response))
  
            }

            feedbackAdded = (response) => {
                console.log("response:",response)
        
                alert("Feedback added successfully ");
                this.props.history.push("/scheduled/userId")
        
            }
        
            errorFeedback = (response) => {

                console.log("response:",response)
                alert("Something went wrong ");
            }
        

            checkFields=()=>{
                const stateData = Object.assign({}, this.state);
                let keys = Object.keys(stateData);
                let isBtnActive = true;
                let formFields = ["hrLeaderAbility","educationSkill","organizationalSkill","communicationSkill","overallRating","interviewResult"];
              //  console.log("==============="+stateData)
                for(var i=0 ; i<formFields.length ; i++){
                    console.log(stateData[formFields[i]])
                    if(stateData[formFields[i]]==undefined || stateData[formFields[i]]==null || stateData[formFields[i]]=="" || this.state.emailIdFlag==false || this.state.contactFlag==false || this.state.birthDateFlag==false){
                    
                       isBtnActive = false;
                     //  console.log("check field",isBtnActive)
                       
                        break;
                    }
                }
                return isBtnActive;
            }
        



    render() {


        return (
            <>
            <table align="center">
            <tr>
                <h2 className="heading" align="center">Generate Feedback </h2><br/>
                <div className="container">

                    <form align="center">
                         <div className="row">
                        <div className="col-md-6">
                        <label for="candidateName">Candidate Name</label>
                                <input type="text" name="candidateName"  readOnly value={this.state.candidateName} />
                                <small id="candidateName" className="form-text text-muted">
                              
                                </small>
                            </div>
                            <div className="col-md-6">
                            <label for="round">Round</label>
                                <input type="text" name="round"  value={this.state.round} className="candidateName" />
                                <small id="round" className="form-text text-muted">

                                </small>
                            </div>
                        </div>

                        <div className="row">
                        <div className="col-md-6">
                        <label for="designation">Designation</label>
                                <input type="text" name="designation"  value={this.state.jobRole} className="candidateName" />
                                <small id="round" className="form-text text-muted">

                                </small>
                            </div>
                            <div className="col-md-6">
                            <label for="interviewerName">Interviewer Name</label>
                                <input type="text" name="interviewerName" value={this.state.interviewerName} className="candidateName" />
                                <small id="interviewerName" className="form-text text-muted">

                                </small>
                            </div>
                        </div>

                        <div className="row">
                        <div className="col-md-6">
                        <label for="experience">Experience</label>
                                <input type="text" name="experience" value={this.state.experience} className="candidateName" />
                                <small id="experience" className="form-text text-muted">

                                </small>
                            </div>
                            <div className="col-md-6">
                            <label for="interviewDate">Interview Date</label>
                                <input type="date" name="interviewDate" value={this.state.dateOfInterview} className="candidateName" />
                                <small id="interviewDate" className="form-text text-muted">

                                </small>
                            </div>
                        </div>  

                        <div className="row">
                        <div className="col-md-6">
                        <label for="hrLeaderAbility"></label>HR Ability<span className="error">*</span>
{/* New  Change Done  */}
                               <p>  <div class="radio-inline">   
                                            <input  type="radio" name="hrLeaderAbility" id="inlineRadio1" className="hrLeaderAbility" onChange={this.handlehrLeaderAbility} value="1"/>
                                            <label class="form-check-label" for="inlineCheckbox1">1</label>
                                            {/* </div>
                                            <div class="checkbox-inline"> */}
                                            <input class="form-check-input"  type="radio" name="hrLeaderAbility" id="inlineRadio2" className="hrLeaderAbility" onChange={this.handlehrLeaderAbility} value="2"/>
                                            <label class="form-check-label" for="inlineCheckbox2">2</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input" type="radio" name="hrLeaderAbility" id="inlineRadio3" className="hrLeaderAbility" onChange={this.handlehrLeaderAbility} value="3"/>
                                            <label class="form-check-label" for="inlineCheckbox2">3</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input" type="radio" name="hrLeaderAbility" id="inlineRadio4" className="hrLeaderAbility" onChange={this.handlehrLeaderAbility} value="4"/>
                                            <label class="form-check-label" for="inlineCheckbox2">4</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input" type="radio" name="hrLeaderAbility" id="inlineRadio5" className="hrLeaderAbility" onChange={this.handlehrLeaderAbility} value="5"/>
                                            <label class="form-check-label" for="inlineCheckbox2">5</label>
                                            </div>
                                            </p> 
                                <small id="technikcalSkill" className="form-text text-muted">
                               
                                </small>
                            </div>
                            <div className="col-md-6">
                            <label for="educationalSkill">Educational Skill</label><span className="error">*</span>
                               <p>  <div class="form-check form-check-inline">
                                            <input class="form-check-input" type="radio" name="educationSkill" id="inlineRadio1" className="educationSkill" onChange={this.handleEducationSkill} value="1"/>
                                            <label class="form-check-label" for="inlineCheckbox1">1</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input" type="radio" name="educationSkill" id="inlineRadio2" className="educationSkill" onChange={this.handleEducationSkill} value="2"/>
                                            <label class="form-check-label" for="inlineCheckbox2">2</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input" type="radio" name="educationSkill" id="inlineRadio3" className="educationSkill" onChange={this.handleEducationSkill} value="3"/>
                                            <label class="form-check-label" for="inlineCheckbox2">3</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input"  type="radio" name="educationSkill" id="inlineRadio4"  className="educationSkill" onChange={this.handleEducationSkill} value="4"/>
                                            <label class="form-check-label" for="inlineCheckbox2">4</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input" type="radio" name="educationSkill" id="inlineRadio5" className="educationSkill"  onChange={this.handleEducationSkill} value="5"/>
                                            <label class="form-check-label" for="inlineCheckbox2">5</label>
                                            </div>
                                            </p> 
                                <small id="educationalSkill" className="form-text text-muted">
                               
                                </small>
                            </div>
                        </div>  

                        <div className="row">
                        <div className="col-md-6">
                        <label for="organizationalSkill">Organizational Skill</label><span className="error">*</span>
                               <p>  <div class="form-check form-check-inline">
                                            <input class="form-check-input"  type="radio" name="organizationalSkill" id="organizationalSkill" className="organizationalSkill" onChange={this.handleorganizationalSkill} value="1"/>
                                            <label class="form-check-label" for="inlineCheckbox1">1</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input" type="radio" name="organizationalSkill" id="organizationalSkill"  className="organizationalSkill" onChange={this.handleorganizationalSkill} value="2"/>
                                            <label class="form-check-label" for="inlineCheckbox2">2</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input"  type="radio" name="organizationalSkill" id="organizationalSkill"  className="organizationalSkill" onChange={this.handleorganizationalSkill} value="3"/>
                                            <label class="form-check-label" for="inlineCheckbox2">3</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input" type="radio" name="organizationalSkill" id="organizationalSkill"  className="organizationalSkill"  onChange={this.handleorganizationalSkill} value="4"/>
                                            <label class="form-check-label" for="inlineCheckbox2">4</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input"  type="radio" name="organizationalSkill" id="organizationalSkill"  className="organizationalSkill" onChange={this.handleorganizationalSkill} value="5"/>
                                            <label class="form-check-label" for="inlineCheckbox2">5</label>
                                            </div>
                                            </p> 
                                <small id="organizationalSkill" className="form-text text-muted">
                               
                                </small>
                            </div>
                            <div className="col-md-6">
                            <label for="communicationalSkill">Communicational Skill</label><span className="error">*</span>
                               <p>  <div class="form-check form-check-inline">
                                            <input class="form-check-input"  type="radio" name="communicationSkill" id="communicationSkill" className="communicationSkill" onChange={this.handlecommunicationalSkill} value="1"/>
                                            <label class="form-check-label" for="inlineCheckbox2">1</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input"  type="radio" name="communicationSkill" id="communicationSkill" className="communicationSkill" onChange={this.handlecommunicationalSkill} value="2"/>
                                            <label class="form-check-label" for="inlineCheckbox2">2</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input"  type="radio" name="communicationSkill" id="communicationSkill" className="communicationSkill" onChange={this.handlecommunicationalSkill} value="3"/>
                                            <label class="form-check-label" for="inlineCheckbox2">3</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input"  type="radio" name="communicationSkill" id="communicationSkill" className="communicationSkill" onChange={this.handlecommunicationalSkill} value="4"/>
                                            <label class="form-check-label" for="inlineCheckbox2">4</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input"  type="radio" name="communicationSkill" id="communicationSkill" className="communicationSkill" onChange={this.handlecommunicationalSkill} value="5"/>
                                            <label class="form-check-label" for="inlineCheckbox2">5</label>
                                            </div>
                                          
                                            </p> 
                                <small id="communicationalSkill" className="form-text text-muted">
                               
                                </small>
                            </div>
                        </div>


                        <div className="row">
                        <div className="col-md-6">
                        <label for="overallrating">Overall Rating</label><span className="error">*</span>
                               <p>  <div class="form-check form-check-inline">
                                            <label class="form-check-label" for="inlineCheckbox1">1</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input"  type="radio" name="overallRating" id="inlineRadio2" className="overallRating" onChange={this.handleRating} value="2"/>
                                            <label class="form-check-label" for="inlineCheckbox2">2</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input" type="radio" name="overallRating" id="inlineRadio3" className="overallRating" onChange={this.handleRating} value="3"/>
                                            <label class="form-check-label" for="inlineCheckbox2">3</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input"  type="radio" name="overallRating" id="inlineRadio4" className="overallRating" onChange={this.handleRating} value="4"/>
                                            <label class="form-check-label" for="inlineCheckbox2">4</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input" type="radio" name="overallRating" id="inlineRadio5" className="overallRating" onChange={this.handleRating} value="5" />
                                            <label class="form-check-label" for="inlineCheckbox2">5</label>
                                            </div>
                                            </p> 
                                <small id="organizationalSkill" className="form-text text-muted">
                               
                                </small>
                      
                            </div>
                            <div className="col-md-6">
                            <label for="overallrating">Result</label><span className="error">*</span>
                               <p> 
                               <div class="form-check form-check-inline">
                                            <input class="form-check-input"  type="radio" name="interviewResult" id="interviewResult" className="interviewResult" onChange={this.handleresult} value="Hire"/>
                                            <label class="form-check-label" for="inlineCheckbox2">Hire</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input" type="radio" name="interviewResult" id="interviewResult" className="interviewResult" onChange={this.handleresult} value="Reject"/>
                                            <label class="form-check-label" for="inlineCheckbox2">Reject</label>
                                            </div>
                               
                            </p>
                            </div>
                        </div>


                        <div className="row">
                        <div className="col-md-6">
                                <label for="review">Review</label><span className="error">*</span>
                                <textarea id="review" class="form-control" name="review" onChange={this.reviewHandle}></textarea>
                                <small id="round" className="form-text text-muted">

                                </small>
                            </div>
                            
                            <span className='nextRound'>
                       {this.state.space}
                               

                        </span>
                     
                            {/* <p> 
                               <div class="form-check form-check-inline">
                                            <input class="form-check-input"  type="radio" name="nextRound" id="inlineRadio4" className="nextRound" disabled={this.state.radiobuttonDisable} onChange={this.handleNextRound} value="Required"/>
                                            <label class="form-check-label" for="inlineCheckbox2">Required</label>
                                            </div>
                                            <div class="form-check form-check-inline">
                                            <input class="form-check-input" type="radio" name="nextRound" id="inlineRadio5" className="nextRound" disabled={this.state.radiobuttonDisable} onChange={this.handleNextRound} value="NotRequired"/>
                                            <label class="form-check-label" for="inlineCheckbox2">Not</label>
                                            </div>
                               
                                    </p>
                                <small id="round" className="form-text text-muted">

                                </small> */}
                           </div>
                       

                        
                           <div className="SubmitDiv" >
                        
                        <Button className="loginButton" onFocus={this.checkFields} disabled={this.state.buttonDisable}  onClick={this.loginButton}  variant="primary" size="lg" active>Add Feedback</Button>
                    </div>
                   


                        </form> 
                       
                    
                   
                </div>

                </tr>
                </table>
            </>


        )
    }
}
export default GenerateFeedbackforHumanResourse