import React,{Component} from 'react';
import './restpassword.css'
import { Button } from 'react-bootstrap';
import axios from 'axios'
class restpassword extends Component{
    constructor(props){
        super();
        this.state={
            password:'',
            confirmpassword:'',
            userId: '',
            errorMessage:'',
            isError:false,
            passwordLength:false,
            passwordLengthError:'',
            buttonDisable:true,
            confirmpasswordLength:false,
            confirmpasswordLengthError:''
        }
    }
   

    handleChange=(event)=>{
        this.setState({
            [event.target.name]:event.target.value,
            userId:this.props.match.params.userId
        })
        
        this.checkPasswordLength(this.state.password)
       
    }

    checkPasswordLength=(password)=>{
        if(password.length<=4){
            this.setState({
            buttonDisable:true,
            passwordLength:true,
            isError:false,
            passwordLengthError:'*Password length must be 6.',
        })
        }else{
            this.setState({
                passwordLength:false,
                isError:false,
            })
    
        }

    }


    checkConfirmPasswordLength=(checkConfirmPassword)=>{
        if(checkConfirmPassword.length<=4){
            this.setState({
            buttonDisable:true,
            confirmpasswordLength:true,
            isError:false,
            confirmpasswordLengthError:'*Confirm Password length must be 6.',
        })
        }else{
            this.setState({
                buttonDisable:false,
                isError:false,
                confirmpasswordLength:false,
            })
    
        }

    }
   
    handleConfirmPassword=(event)=>{
        this.setState({
            [event.target.name]:event.target.value,
        })
        this.checkConfirmPasswordLength(this.state.confirmpassword)

    }
    onKeyDown = (event) => {
         
        if (event.key === 'Enter') {
           if(this.state.confirmpasswordLength === false && this.state.passwordLength === false ){
              event.preventDefault();
               event.stopPropagation();
                this.handleSubmit();
                }
          }
      }
   
    handleSubmit=()=>{
    
        if(this.state.password===this.state.confirmpassword){
        this.setState({
            isError:false
        })
        axios.post('http://localhost:8080/changepassword',this.state).then(response=>{this.AuthorizedUser(response)}).catch(response=>{this.UnauthorizedUser(response)});
        }else{
            this.setState({
                isError:true,
                errorMessage:'*Confirm Password and Password do not match.',
            })
        
        }
    
    }

    AuthorizedUser=(response)=>{
        alert("Password is changed successfully ");
        this.props.history.push('/');
    }

    UnauthorizedUser=(error,info)=>{
        this.setState({ hasError: true });
        console.log("==========" + error)
        console.log(error.response.status)
        if (error.response.status === 404) {
            this.setState({
                isError:true,
                errorMessage:'*Username not found.',

            })
        }
        if (error.response.status = 422) {
            this.setState({
                isError: true,
                errorMessage: 'All feilds are mandatory.',
            })
        }

        if (error.response.status = 400) {
            this.setState({
                isError: true,
                errorMessage: 'Please enter valid data.',
            })
        }

        //  this.props.history.push('/InValidURL');
    }
    render(){
        
        return(
            <div className="login">
                  <div class="loginForm" align="center" >
               <div class="col-md-12 col-sm-12" class="logo">
                  <h1>Vahak IMS</h1>
                </div>
            
               
              <div class="col-md-12 col-sm-12 ">
                {/* <div  class="passwordDiv"> */}
                <span  class="password-label">Password</span><span class="error">*</span><input type="password" name="password" class="userpassword" onChange={this.handleChange} placeholder="Enter Password" required/><br></br>
                {this.state.passwordLength && <span className='error'>{this.state.passwordLengthError}</span>}
                </div>
               

                <div class="col-md-12 col-sm-12 ">
                {/* <div class="confirmpasswordDiv"> */}
                <span  class="confirmpassword-label">Confirm Password</span><span class="error">*</span><input type="password" name="confirmpassword"  onKeyDown={this.onKeyDown} class="confirmpassword"  onChange={this.handleConfirmPassword} placeholder="Enter Password" required/><br></br>
                {this.state.confirmpasswordLength && <span className='error'>{this.state.confirmpasswordLengthError}</span>}
                
                </div>
               

                {/* <div>
                <input type="text" name="userId"  value= {this.props.match.params.userId}></input>
                </div> */}
                <div   class="col-md-12 col-sm-12 ">
                <Button  lassName="submitButtom"   disabled={this.state.buttonDisable}  variant="primary" size="lg" active onClick={this.handleSubmit}>Submit</Button><br></br>
                
             </div>
            
              
                {/* <div>
                <Link to="/forgotpassword" >Forgot Password</Link>
                </div> */}
                  {this.state.isError && <div className="alert alert-danger">{this.state.errorMessage}</div>}
            </div>
         
           
                            </div>
        )
    }
}

export default restpassword;