import React, { Component } from 'react'
import InterviewOfToday from '../interviewerComponent/InterviewOfToday'
import FeedbackFilterHorizontalChart from '../charts/FeedbackFilterHorizontalChart'
import InterviewerFilterDonutChart from '../charts/InterviewerFilterDonutChart'

import './InterviewerDashboard.css'

class InterviewerDashboard extends Component{
    render(){
        return(
            <>
             
            <React.Fragment>
            <h1 align="center" id='titletoday'> Today's Interview List</h1>
                <div className="forScrolling">
                 <InterviewOfToday/>
                  </div>
                 
                <div className="row">
                         <div className="col-md-6">
                        <FeedbackFilterHorizontalChart/>
                        </div> 
                        <div className="col-md-6">
                        <InterviewerFilterDonutChart/>
                        </div>
                </div>
            </React.Fragment>
            </>
        )
    }

}
export default InterviewerDashboard