import React from 'react';
import {HorizontalBar} from 'react-chartjs-2';
import CandidateDataService from '../../api/RestApi/CandidateDataService.js'



export default class LineChart extends React.Component {

     


    constructor() {
        super();
        this.state = {
          candidates: [],
          candidateId: '',
          getCandidateList: false,
          errorMesssge: '',
          chart:'',
          i:'',
          count:'',
         
          scheduleInterviewCount:0,
          unscheduledInterviewCount:0,
          penddingApprovalCount:0,
        }
      }


    componentDidMount() {
        CandidateDataService.getListOfCandidates().then(response => {
          if(response.data.dataList.length!=0){
          this.setState({
            candidates: response.data.dataList,
            getCandidateList: true,

           
          })
          
         
         
        }else{
          this.setState({
            getCandidateList: false,
            errorMesssge: "Record Not Found.",
          })
        }
        }).catch(response => {
          this.setState({
            getCandidateList: false,
            errorMesssge: "Something Went Wrong.",
          })
        })
    
      }

      
    

  
  render() {

    let userRow = this.state.candidates.map((candidates, i) => {
      if(candidates.interviewId===0){
        this.state.unscheduledInterviewCount=this.state. unscheduledInterviewCount+1;
      }else if(candidates.interviewId!=0 && candidates.interviewStatus==="Accepted"){
        this.state.scheduleInterviewCount=this.state. scheduleInterviewCount+1;
      }else if(candidates.interviewId!=0 && candidates.interviewStatus==="scheduled"){
        this.state.penddingApprovalCount=this.state.penddingApprovalCount+1;
      }
       
      
        return (

            this.state.chart={
                labels: ['Scheduled Interview', 'Unscheduled Interview','Pending Interview'],
            datasets: [
            {
            label: 'Interview Status',
            backgroundColor: [
             '#ADD8E6',
              '#C9DE00',
             '#fed8b1',
             '#0000ff',
             
            ],
            hoverBackgroundColor: [
            '#501800',
            '#4B5000',
            '#175000',
            
            ],
            data: [this.state.scheduleInterviewCount,this.state.unscheduledInterviewCount,this.state.penddingApprovalCount],
           
            }
              ]
             }
        

        
        );
      });
  
      
    return (
      <div>
      { this.state.getCandidateList &&  <HorizontalBar
          data={this.state.chart}
          width={"600%"} 
          height={"315%"}
          options={{
            title:{
              display:true,
              text:'Interview Status',
              fontSize:20
            },
            legend:{
              display:false,
              position:'right'
            },
            scales: {
              xAxes: [{
                ticks: {
                  beginAtZero: true,
               
                }
              }]
            }
          }}
        />
        }
           {!this.state.getCandidateList && <div>
        <h1>No Data</h1>
        </div>}
      </div>
   
    );
  }
}

