import React from "react"
import { Component } from "react"
import CandidateDataService from '../../api/RestApi/JobPostService.js'
import axios from 'axios'
import './ShowJobPosting.css'
import Ripples from 'react-ripples'
import Information from '../../api/RestApi/CandidateDataService.js'
import { BrowserRouter as Router, Route, Switch, Link } from 'react-router-dom';
import '../../bootstrap.css';
// import '../HumanResourseComponent/ViewCandidateTable.css';

import Moment from 'react-moment';
import { Button, Modal, ModalBody, ModalFooter, ModalHeader } from 'reactstrap';
class ShowJobPosting extends Component {
    constructor() {
        super();
        this.state = {
            candidates: [],
            candidateId: '',
            getCandidateList: false,
            errorMesssge: '',
            modalupdateisOpen: false,
            modaldeleteisOpen: false,
            managerId: sessionStorage.getItem("userId"),
            //New

            jobOpening: '',
            buttonDisable: true,
            exprianceRange: '',
            responseData: [],
            group: [],
            requirementGroup: '',
            requirementDepartment: '',
            requirementDesignation: '',
            requirmentName: '',

            pesDepartment: [],
            asicDepartment: [],
            aerospaceDepartment: [],
            department: [],
            designationArray: [],
            designation: [],
            requirments: [],
            userId: ''
        }
    }

    componentDidMount() {
        CandidateDataService.retrieveJobPosting().then(response => {
            if (response.data.dataList.length != 0) {
                this.setState({
                    candidates: response.data.dataList,
                    getCandidateList: true
                })
            } else {
                this.setState({
                    getCandidateList: false,
                    errorMesssge: "Record Not Found.",
                })
            }

        }).catch(response => {
            this.setState({
                getCandidateList: false,
                errorMesssge: "Something Went Wrong.",
            })
        })

    }


    toggleUpdateModal = (managerId) => {
        Information.getInfomation().then(response => { this.handleInformation(response) }).catch(response => { this.handleError(response) })
        this.setState({

            modalupdateisOpen: !this.state.modalupdateisOpen, buttonDisable: true

        })


    }

    toggleCloseupdateModal = () => {

        this.setState({ modalupdateisOpen: !this.state.modalupdateisOpen })
        this.state.jobOpening = ''
        this.state.responseData = []
        this.state.group = []
        this.state.pesDepartment = []
        this.state.asicDepartment = []
        this.state.aerospaceDepartment = []
        this.state.department = []
        this.state.designationArray = []
        this.state.designation = []
        this.state.requirments = []
        this.state.requirementGroup = ''
        this.state.requirementDepartment = ''
        this.state.requirementDesignation = ''
        this.state.requirmentName = ''
    }
    //HandleInformation
    handleInformation = (response) => {
        console.log(response.data.dataList)

        this.setState({
            userId: sessionStorage.getItem("userId"),
            responseData: response.data.dataList
        })

        this.state.responseData.map((information) => {
            if (information.informationType === "Group") {

                this.setState({
                    group: this.state.group.concat([{ id: information.informationId, text: information.informationName }])
                })
            }
            if (information.informationType === "Department") {
                if (information.groupName === "PES") {
                    this.setState({
                        pesDepartment: this.state.pesDepartment.concat([{ id: information.informationId, text: information.informationName }])
                    })
                } else if (information.groupName === "ASIC") {
                    this.setState({
                        asicDepartment: this.state.asicDepartment.concat([{ id: information.informationId, text: information.informationName }])
                    })
                } else if (information.groupName === "Aerospace") {
                    this.setState({
                        aerospaceDepartment: this.state.aerospaceDepartment.concat([{ id: information.informationId, text: information.informationName }])
                    })
                }
            }
            if (information.informationType === "Designation") {
                this.setState({
                    designationArray: this.state.designationArray.concat([{ id: information.informationId, text: information.informationName }])
                })
            }
            if (information.informationType === "Requirement") {
                this.setState({
                    requirments: this.state.requirments.concat([{ id: information.informationId, text: information.informationName }])
                })
            }



        })
    }

    handleError = (response) => {
        console.log("Error")
    }

    UpdateCandidate = () => {

        axios.post('http://localhost:8080/postjobrequirment', this.state).then(response => { this.handleJobPostingSuccess(response) }).catch(response => { this.handleJobPostingError(response) })
        this.setState({ modalupdateisOpen: !this.state.modalupdateisOpen })
    }

    handleJobPostingSuccess = (response) => {
        alert("Job Requirements Successfully Posted")
        this.state.jobOpening = ''
        this.state.responseData = []
        this.state.group = []
        this.state.pesDepartment = []
        this.state.asicDepartment = []
        this.state.aerospaceDepartment = []
        this.state.department = []
        this.state.designationArray = []
        this.state.designation = []
        this.state.requirments = []
        this.state.requirementGroup = ''
        this.state.requirementDepartment = ''
        this.state.requirementDesignation = ''
        this.state.requirmentName = ''

        this.componentDidMount();
    }

    //Hadle Error
    handleJobPostingError = (error, errorinfo) => {
        if (error.response.status === 422) {
            alert("Please Enter Valid Information.")
            // window.history.back()
        }
    }

    //OnKeyDown

    allowNumbersForExperience(event) {

        if (!(event.ctrlKey || event.altKey
            || (47 < event.keyCode && event.keyCode < 58 && event.shiftKey == false)
            || (95 < event.keyCode && event.keyCode < 106)
            || (event.keyCode == 8) || (event.keyCode == 9)
            || (event.keyCode > 34 && event.keyCode < 40)
            || (event.keyCode == 46) || (event.keyCode == 190) || (event.keyCode == 110)))
            event.preventDefault();
    }

    numberOfJobOpeningChange = (event) => {


        this.setState({
            jobOpening: event.target.value
        })
        this.state.jobOpening = event.target.value

        let isSubmitActive = this.checkFields();
        if (isSubmitActive) {
            this.setState({
                buttonDisable: false
            })
        } else {
            this.setState({
                buttonDisable: true
            })
        }
    }

    checkFields = () => {
        const stateData = Object.assign({}, this.state);
        let keys = Object.keys(stateData);
        let isBtnActive = true;
        let formFields = ["jobOpening", "exprianceRange", "requirementGroup", "requirementDepartment", "requirementDesignation", "requirmentName"];
        console.log(stateData)
        for (var i = 0; i < formFields.length; i++) {
            console.log(stateData[formFields[i]])
            if (stateData[formFields[i]] == undefined || stateData[formFields[i]] == null || stateData[formFields[i]] == "" || this.state.emailIdFlag == false || this.state.contactFlag == false || this.state.birthDateFlag == false) {

                isBtnActive = false;
                console.log("check field", isBtnActive)

                break;
            }
        }
        return isBtnActive;
    }
    //Change Expriance    
    onChangeExpriance = (event) => {
        this.setState({
            exprianceRange: event.target.value,
        })
        this.state.exprianceRange = event.target.value
        let isSubmitActive = this.checkFields();
        if (isSubmitActive) {

            this.setState({
                buttonDisable: false
            })
        }
        if (event.target.value === "") {
            this.setState({
                buttonDisable: true
            })
        }
    }

    //Change Group
    onChangeGroup = (event) => {
        this.state.department = []
        this.state.designation = []
        this.setState({
            requirementGroup: event.target.value
        })
        this.state.requirementGroup = event.target.value
        if (event.target.value === "PES") {
            this.setState({
                department: this.state.pesDepartment
            })
        } else if (event.target.value === "ASIC") {
            this.setState({
                department: this.state.asicDepartment
            })
        } else if (event.target.value === "Aerospace") {
            this.setState({
                department: this.state.aerospaceDepartment
            })
        }


        let isSubmitActive = this.checkFields();
        if (isSubmitActive) {

            this.setState({
                buttonDisable: false
            })
        }
        if (event.target.value === "") {
            this.setState({
                buttonDisable: true
            })
        }
    }

    //Change Department

    onDepartmentChange = (event) => {
        this.state.designation = []
        this.setState({
            requirementDepartment: event.target.value,

        })
        if (event.target.value != "") {
            this.setState({
                designation: this.state.designationArray
            })
        }

        this.state.requirementDepartment = event.target.value

        let isSubmitActive = this.checkFields();
        if (isSubmitActive) {

            this.setState({
                buttonDisable: false
            })
        }
        if (event.target.value === "") {
            this.setState({
                buttonDisable: true
            })
        }
    }

    //Change Designation
    onDesignationChange = (event) => {
        this.setState({
            requirementDesignation: event.target.value
        })
        this.state.requirementDesignation = event.target.value

        let isSubmitActive = this.checkFields();
        if (isSubmitActive) {

            this.setState({
                buttonDisable: false
            })
        }
        if (event.target.value === "") {
            this.setState({
                buttonDisable: true
            })
        }
    }

    onChangeRequirments = (event) => {
        this.setState({
            requirmentName: event.target.value
        })
        this.state.requirmentName = event.target.value

        let isSubmitActive = this.checkFields();
        if (isSubmitActive) {

            this.setState({
                buttonDisable: false
            })
        }
        if (event.target.value === "") {
            this.setState({
                buttonDisable: true
            })
        }

    }
    render() {

        let userRow = this.state.candidates.map((candidates, i) => {

            return (

                <tr >
                    <td></td>
                    <td>{candidates.requirementGroup}</td>
                    {/* <td>{candidates.address}</td> */}
                    <td x-ms-format-detection="none" >{candidates.requirementDepartment}</td>
                    <td>{candidates.requirementDesignation}</td>
                    <td><Moment format="DD/MM/YYYY" date={candidates.jobPostedDate}></Moment></td>
                    <td>{candidates.requirmentName}</td>
                    <td align="center">{candidates.jobOpening}</td>
                    <td>{candidates.exprianceRange}</td>
                    <td>{candidates.managerName}</td>
                    <td>{candidates.totalNoOfOpeing}</td>



                </tr>
            );
        });

        return (
            <div>


                <div className="col-md-12 col-sm-12">
                    <a type="button" onClick={this.toggleUpdateModal.bind(this, this.state.managerId)} data-toggle="tooltip" title="Add Job Openings" className="fa fa-plus fa-2x float-right "  > </a>
                    <Modal isOpen={this.state.modalupdateisOpen} >
                        <ModalHeader toggle={this.toggleCloseupdateModal.bind(this)}>
                            Job Posting
            </ModalHeader>
                        <ModalBody>




                            {/* <div className="row ">
                
                       <div className="col-md-6"> */}

                            <div className="item form-group">
                                <label className="col-form-label col-md-5 col-sm-5 label-align"
                                    htmlFor="candidateName">Number Opening<span className="error">*</span></label>
                                <input type="text" name="jobOpening" onKeyDown={this.allowNumbersForExperience} maxLength="3" className="col-form-label col-md-7 col-sm-7" onChange={this.numberOfJobOpeningChange} list="autocompleteOff" value={this.state.jobOpening} autoComplete="off" placeholder="Number Of Job Opening" required />

                            </div>

                            <div className="item form-group">

                                <label className="col-form-label col-md-5 col-sm-5 label-align"
                                    htmlFor="requirement">Select Skillset <span className="error">*</span></label>

                                <select id="requirement" className="requirement" onChange={this.onChangeRequirments} className="col-form-label col-md-7 col-sm-7 qualification">
                                    <option value="">Select Skillset</option>
                                    {this.state.requirments.map(requirments => <option key={requirments.id} value={requirments.text}>{requirments.text}</option>)}
                                </select>
                            </div>


                            {/* </div> */}


                            <div className="item form-group">

                                <label htmlFor="requirement" className="col-form-label col-md-5 col-sm-5 label-align">Group Name<span className="error">*</span></label>

                                <select id="group" className="group" onChange={this.onChangeGroup} className="col-form-label col-md-7 col-sm-7 qualification">
                                    <option value="">Group Name</option>
                                    {this.state.group.map(group => <option key={group.id} value={group.text}>{group.text}</option>)}
                                </select>

                            </div>

                            <div className="item form-group">

                                <label htmlFor="requirement" className="col-form-label col-md-5 col-sm-5 label-align">Department Name<span className="error">*</span></label>

                                <select id="department" className="department" onChange={this.onDepartmentChange} className="col-form-label col-md-7 col-sm-7 qualification">
                                    <option value="">Department Name</option>
                                    {this.state.department.map(department => <option key={department.id} value={department.text}>{department.text}</option>)}
                                </select>


                            </div>

                            <div className="item form-group">

                                <label htmlFor="requirement" className="col-form-label col-md-5 col-sm-5 label-align">Designation Name<span className="error">*</span></label>



                                <select id="Designation" className="Designation" onChange={this.onDesignationChange} className="col-form-label col-md-7 col-sm-7 qualification">

                                    <option value="">Designation Name</option>
                                    {this.state.designation.map(designation => <option key={designation.id} value={designation.text}>{designation.text}</option>)}


                                </select>

                            </div>

                            <div className="item form-group">
                                <label htmlFor="exprianceRange" className="col-form-label col-md-5 col-sm-5 label-align">Work Experience<span className="error">*</span></label>

                                <select id="exprianceRange" className="exprianceRange" onChange={this.onChangeExpriance} className="col-form-label col-md-7 col-sm-7 qualification">
                                    <option value="">Work Experience</option>
                                    <option value="0">Fresher</option>
                                    <option value="1-2">0-2</option>
                                    <option value="2-4">2-4</option>
                                    <option value="4-6">4-6</option>
                                    <option value="6-8">6-8</option>
                                    <option value="8-10">8-10</option>
                                    <option value="10+">10+</option>

                                    {/* <option value="">Select Interviewer Department </option>
                                       {this.state.department.map(department => <option key={department.id} value={department.text}>{department.text}</option>)} */}


                                </select>






                                <div >

                                </div>



                            </div>




                        </ModalBody>
                        <ModalFooter>
                            <Ripples>
                                <Button onClick={this.UpdateCandidate.bind(this)} onFocus={this.checkFields} disabled={this.state.buttonDisable} type="submit" className="btn btn-success " > Add </Button>
                            </Ripples>
                            <Button onClick={this.toggleCloseupdateModal.bind(this)} type="button" className="btn btn-default" >Cancel</Button>
                        </ModalFooter>
                    </Modal>

                    {!this.state.getCandidateList && <div>

                        <h1 align="center" id='title'>{this.state.errorMesssge}</h1>


                    </div>}

                    {this.state.getCandidateList &&
                        <div className="JobPostingTable">



                            <table className="table" id="JobPostingTable">
                                <thead>

                                    <tr>
                                        <th align="center">Index</th>
                                        <th align="center">Group Name</th>
                                        {/* <th>Address </th> */}

                                        <th align="center">Department Name</th>
                                        <th align="center">Designation Name</th>
                                        <th align="center">Date</th>

                                        <th align="center">Skill Set</th>
                                        <th align="center">Post Number Of Job Opening</th>
                                        <th align="center">Expriance Range</th>
                                        <th align="center">Hiring Manager</th>
                                        <th align="center">Total Number Of Job Opening</th>












                                    </tr>
                                </thead>
                                <tbody>
                                    {userRow}
                                </tbody>
                            </table>
                        </div>
                    }
                </div>

            </div>
        )
    }
}
export default ShowJobPosting;