import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch, Link } from "react-router-dom";
import AuthUser from "./AuthUser.js";
import { withRouter } from "react-router";
import Collapsible from "react-collapsible";
import "./SideMenu.css";
import Dashboard from '../HumanResourseComponent/Dashboard.jsx'

class SideMenu extends Component {
  render() {
    const isLogin = AuthUser.checkUser();
    const isHumanresource = AuthUser.isHumanResource();
    const isAdmin = AuthUser.isAdmin();
    const isInterviewer = AuthUser.isInterviewer();
    const isManager = AuthUser.isManager();
    const validUser = AuthUser.isValidUser();

    return (
      <div>
        {isLogin && (
          <div className="padding-0 sidemenu">
            <div className="icon-box">
              <img src="logo192.png" alt="..." className="site-icon" />
            </div>

            {/* HumanResource Dashbord */}

            {isLogin && isHumanresource && (
              <div className="sidenav">
               

                {/* SideMenu for HumanResource */}

                <div className="Menu_First">
                  <Collapsible trigger={<a className="btn btn-collapsible"><span><span className="fa fa-home fa-collapsible"> Dashboard</span> <span className="fa fa-chevron-down"></span></span></a> }>
                    <ul className="nav_child_menu">
                     
                       <li>
                       
                        <Link to="/Dashboardhr" >Dashboard</Link>
                      </li> 
                    </ul>
                  </Collapsible>
                </div>

                
                <div className="Menu_First">
                  <Collapsible trigger={<a className="btn btn-collapsible"><span><span className="fa fa-edit fa-collapsible"> Candidate Management</span> <span className="fa fa-chevron-down"></span></span></a> }>
                    <ul className="nav_child_menu">
                      <li>
                        <Link to="/AddCandidate">Add Candidate</Link>
                      </li>
                      <li>
                        <Link to="/ShowCandidate">Show Candidate</Link>
                      
                      </li>
                       {/* <li>
                       
                        <Link to="/dashboard" >Dashboard</Link>
                      </li>  */}
                    </ul>
                  </Collapsible>
                </div>

                <div className="Menu_Secound">
                  <Collapsible trigger={<a className="btn btn-collapsible"><span><span className="fa fa-table fa-collapsible"> Interview Management</span><span className="fa fa-chevron-down"></span></span> </a> }>
                    <ul className="nav_child_menu">
                      <li >
                        <Link to="/UnScheduledInterview"> Reports Of Candidate</Link>
                      </li>
                      <li>
                        <Link to="/ScheduledInterview">Scheduled Interview </Link>  
                      </li>
                      <li>
                        <Link to="/PenddingInterviewApproval">Pending Interview Approval</Link>
                      </li>
                      
                    </ul>
                  </Collapsible>
                </div>
                <div className="Menu_Secound">
                  <Collapsible trigger={<a className="btn btn-collapsible"><span><span  className="fa fa-table fa-collapsible">Candidates Status Report</span><span className="fa fa-chevron-down"></span></span></a>}>
                    <ul  className="nav_child_menu">
                    
                        <li><Link to="/HireCandidate">Hire Candidate</Link></li>
                        <li><Link to="/RejectedCandidate">Rejected Candidate</Link></li>
                   

                    </ul>
                    </Collapsible>

             
               </div>
                <div className="Menu_Secound">
                  <Collapsible trigger={ <a className="btn btn-collapsible"><span className="fa fa-envelope"> Mail</span> </a> }>
                    <ul className="nav_child_menu"></ul>
                  </Collapsible>
                </div>
              </div>
            )}

            {/* Admin Dashbord */}

            {isLogin && isAdmin && (
              <div className="sidenav">
                <div className="Menu_First">
                  <Collapsible trigger={ <a className="btn btn-collapsible"><span><span className="fa fa-home fa-collapsible"> {" "} &nbsp;Dashboard</span><span className="fa fa-chevron-down"></span></span> </a> }>
                    <ul className="nav_child_menu">
                      <li>
                        <Link to="/adminDashboard">Dashboard</Link>
                      </li>
               
                                            
                    </ul>
                  </Collapsible>
                </div>

                {/* SideMenu for Admin */}
                <div className="Menu_First">
                  <Collapsible trigger={ <a className="btn btn-collapsible"><span><span className="fa fa-edit fa-collapsible"> {" "} &nbsp;User Data Management</span><span className="fa fa-chevron-down"></span></span> </a> }>
                    <ul className="nav_child_menu">
                      <li>
                        <Link to="/addUser">Add User</Link>
                      </li>
                       
                      <li>
                        <Link to="/viewAllUser">View User</Link></li>
                        {/* <li> <Link to="/viewAllUserChart">chart</Link></li> */}
                        
                      
                    </ul>
                  </Collapsible>
                </div>

                <div className="Menu_Secound">
                  <Collapsible trigger={ <a className="btn btn-collapsible"> <span className="fa fa-envelope">&nbsp; Mail</span> </a>}>
                    <ul className="nav_child_menu"></ul>
                  </Collapsible>
                </div>
              </div>
            )}
            {/* Interviewer Dashbord */}

            {isLogin && isInterviewer && (
              <div className="sidenav">
               
               <div className="Menu_First">
                  <Collapsible trigger={<a className="btn btn-collapsible"><span><span className="fa fa-home fa-collapsible">&nbsp;Dashboard</span><span className="fa fa-chevron-down"></span></span> </a> }>
                    <ul className="nav_child_menu">
                      <li>
                        <Link to="/InterviewerDashboard"> Dashboard</Link>
                      </li>
                    </ul>
                  </Collapsible>
                </div>

                {/* SideMenu for Interviewer */}
                <div className="Menu_First">
                  <Collapsible trigger={<a className="btn btn-collapsible"><span><span className="fa fa-user fa-collapsible">&nbsp;Interviewer Management</span><span className="fa fa-chevron-down"></span></span> </a> }>
                    <ul className="nav_child_menu">
                      <li>
                        <Link to="/GiveFeedback/userId">Give Feedback</Link>
                      </li>
                      <li>
                        <Link to="/ViewAllFeedback">View All Feedback</Link>
                      </li>
                    </ul>
                  </Collapsible>
                </div>

                <div className="Menu_Secound">
                  <Collapsible trigger={<a className="btn btn-collapsible"> <span><span className="fa fa-table fa-collapsible">&nbsp;Interview Management</span><span className="fa fa-chevron-down"></span></span></a> }>
                    <ul className="nav_child_menu">
                
                      <li>
                        <Link to="/scheduled/userId">Scheduled Interview </Link> 
                      </li>

                      <li>
                        <Link to="/pendingInterviewApprovalList">Pending Interview Approval</Link>
                      </li>
                    </ul>
                  </Collapsible>
                </div>

                <div className="Menu_Secound">
                  <Collapsible trigger={<a className="btn btn-collapsible"><span><span className="fa fa-table fa-collapsible">&nbsp;Candidate Reports</span><span className="fa fa-chevron-down"></span></span></a>}>
                    <ul className="nav_child_menu">
                      <li>
                        <Link to="/AcceptedCandidate">Accepted Candidates</Link>
                      </li>
                      <li>
                        <Link to="/RejectedCandidateList">Rejected Candidates</Link>
                      </li>
                      <li>
                        <Link to="/HiredCandidate">Hired Candidates</Link>
                      </li>
                    </ul>
                  </Collapsible>
                </div>

                <div className="Menu_Secound">
                  <Collapsible trigger={ <a className="btn btn-collapsible"><span className="fa fa-envelope"> Mail</span></a>}>
                    <ul className="nav_child_menu"></ul>
                  </Collapsible>
                </div>
              </div>
            )}

{/* Manager Dashboard */}
{isLogin && isManager && (
              <div className="sidenav">
                <div className="Menu_First">
                  <Collapsible trigger={ <a className="btn btn-collapsible"><span><span className="fa fa-home fa-collapsible"> {" "} &nbsp;Dashboard</span><span className="fa fa-chevron-down"></span></span> </a> }>
                    <ul className="nav_child_menu">
                      <li>
                        <Link to="#">Dashboard</Link>
                      </li>
               
                                            
                    </ul>
                  </Collapsible>
                </div>

                
                <div className="Menu_First">
                  <Collapsible trigger={ <a className="btn btn-collapsible"><span><span className="fa fa-edit fa-collapsible">Requirement Management</span><span className="fa fa-chevron-down"></span></span> </a> }>
                    <ul className="nav_child_menu">
                      <li>
                        <Link to="/JobPosting">Add Job Requirement</Link></li>
                       <li> <Link to="/candidateQualificationChart">Chart</Link>
                      </li>
                       
                      <li>
                        <Link to="/ShowJobPosting">Show Job Requirement</Link></li>
                        {/* <li> <Link to="/viewAllUserChart">chart</Link></li> */}
                        
                      
                    </ul>
                  </Collapsible>
                </div>

                <div className="Menu_Secound">
                  <Collapsible trigger={ <a className="btn btn-collapsible"> <span className="fa fa-envelope">&nbsp; Mail</span> </a>}>
                    <ul className="nav_child_menu"></ul>
                  </Collapsible>
                </div>
              </div>
            )}
          </div>
         
        )}
      </div>
    );
  }
}

export default withRouter(SideMenu);
