import React from 'react';
import {HorizontalBar} from 'react-chartjs-2';
import UserDataService from '../../api/RestApi/UserDataService.js'
import './viewAllUserChart.css'



export default class ViewAllUserChart extends React.Component {

    constructor() {
        super();
        this.state = {
          users: [],
          userId: '',
          getUserList: false,
          nullMessage: '',
          status:'',
          i:'',
          count:'',
          totalCount:0,
          adminCount:0,
          interviewerCount:0,
          humanResourceCount:0,
          managerCount:0
          
        }
      }


    componentDidMount() {
      UserDataService.retrieveAllUsers().then(response => {
          if(response.data.dataList.length !=0){
          this.setState({
            users: response.data.dataList,
            getUserList: true,

           
          })
        
        }else{
          this.setState({
            getUserList: false,
            nullMessage: "No Record Found",
          })
        }
        }).catch(response => {
          this.setState({
            getUserList: false,
            nullMessage: "Something Went Wrong.",
          })
        })
    
      }

  render() {

    let userRow = this.state.users.map((users, i) => {
    
      this.state.totalCount=this.state.totalCount+1;
       
         if(users.user_type === "Admin"){

          this.state.adminCount=this.state.adminCount+1;
          this.state.totalCount=this.state.totalCount-1;

        }else if(users.user_type==="Interviewer"){

          this.state.interviewerCount=this.state.interviewerCount+1;
          this.state.totalCount=this.state.totalCount-1;

        }else if(users.user_type==="HumanResource"){

            this.state.humanResourceCount=this.state.humanResourceCount+1;
            this.state.totalCount=this.state.totalCount-1;

        }
        else if(users.user_type==="Manager"){

          this.state.managerCount=this.state.managerCount+1;
          this.state.totalCount=this.state.totalCount-1;

      }
        
          console.log("Admin"+this.state.adminCount)
          console.log("Interviewer"+this.state.interviewerCount)
          console.log("HR"+this.state.humanResourceCount)

        return (

            this.state.status={
                labels: ['Admin', 'Interviewer', 'HumanResource','Manager'],
            datasets: [
            {
              
             label: "Overall user",
              
             
            backgroundColor: [
              // '#6800B4',
              '#fed8b1',
              '#ADD8E6',
              '#c2ff9b',
              '#2FDE00',
              '#B21F00',
            
            ],
            
            hoverBackgroundColor: [
                // '#35014F',
                '#CBAD8E',
                '#85b0bd',
                '#4B5000',
                '#175000',
                '#501800',
            ],
            data: [this.state.adminCount,this.state.interviewerCount,this.state.humanResourceCount,this.state.managerCount],
           
            },
           
              ]
             }
        

        
        );
      });
  
      
    return (
      <div>
        {/* <h1 align="center" id='titlechart'>User Chart</h1> */}
        {!this.state.getUserList && <div>
              <h1 align="center" id='title'>{this.state.nullMessage}</h1>
              </div>}
      {this.state.getUserList &&
        <HorizontalBar
        
          data={this.state.status}
          width={"800"}
          height={"200"}
          
          options={{
            responsive: true, 
            maintainAspectRatio: true,
            
          
            title:{
              display:false,
              text:'Overall User',
              fontSize:25,
              padding:10,
              fontFamily:'Times New Roman' ,
              textAlign:'center',
              fontColor:'black'

              
            
            },
            legend:{
              display:false,
              align:'center',
               position:'right',
             
              
              
            },scales: {
              xAxes: [{
                ticks: {
                  //  min: 0,
                   beginAtZero:true,
                   stepSize:1,
                   backdropColor:'#2A3F54',
                }
              }]
            }
           
            
          }}
        />}
        </div>
     
    );
  }
}

