import React, { Component } from 'react';
import {Doughnut} from 'react-chartjs-2';
import InterviewerDataService from '../../api/RestApi/InterviewerDataService.js'
import './viewAllUserChart.css'


export default class InterviewerFilterDonutChart extends React.Component {

constructor(){
    super();

    this.state = {
        feedbacks :[],
        interviews:[],
        interviewId:'',
        feedbackId:'',
        candidateId: '',
        getFeedbackList : false,
        getInterviewList : false,

        userId: sessionStorage.getItem("userId"),
        nullMessage:'',
        statusfeedback:'',
        statusinterview:'',
        errorMesssge: '',
        dateOfInterview:'',
        interviewStatus:'',
        feedbackTodayCount:0,
        feedbackYesterdayCount:0,
        feedbackLastweekCount:0,
        totalCount:0,

        acceptedInterviewCount:0,
        rejectedInterviewCount:0,
    }
}

componentDidMount() {
    InterviewerDataService.getInterviewByInterviewerId(this.state.userId).then(
        response => {
  
          if(response.data.dataList.length!= 0){
  
              this.setState({ 
                  interviews: response.data.dataList ,
                  getInterviewList:true}) 
          }
             else{
              this.setState({ interviews: response.data.dataList ,
                  getInterviewList:false,
                  errorMesssge:'No Interview Is Scheduled Yet'
                }) 
          }
      }).catch(response=>{
            this.setState({ getInterviewList:false,
              errorMesssge: "Something Went Wrong.", })
      })
    
}

render(){

        let interviewRow = this.state.interviews.map((interviews) => {
                   
                if(interviews.interviewId!=null && interviews.interviewStatus === 'Accepted'){ 
                    this.state.acceptedInterviewCount = this.state.acceptedInterviewCount +1;
                }
    
                 if(interviews.interviewId!=null && interviews.interviewStatus === 'Rejected'){
                    this.state.rejectedInterviewCount = this.state.rejectedInterviewCount +1;
                }
               
             
              return (
      
                  this.state.statusinterview={
                      labels: ['Accepted', 'Rejected'],
                  datasets: [
                  {
                  label: 'Interview',
                  backgroundColor: [
                  '#008000',

                   '#B21F00',
                   '#2FDE00',
                   '#00A6B4',
                   '#6800B4'
                  ],
                  hoverBackgroundColor: [
                    '#32CD32',

                  '#501800',
                  '#175000',
                  '#003350',
                  '#35014F'
                  ],
                  data: [this.state.acceptedInterviewCount,this.state.rejectedInterviewCount],
                 
                  }
                    ]
                   }
              
              );
            });
        return (
          <div> <h1 align="center" id='titlechart'>Interview Chart</h1>
          {this.state.getInterviewList &&<div>  
              <Doughnut
                data={this.state.statusinterview}
                options={{
                  responsive: true, 
                  maintainAspectRatio: true,

                  title:{
                    display:true,
                    text:'Average interview',
                    fontSize:20
                  },
                  legend:{
                    display:true,
                    position:'right'
                  },
                  
                }}
              />
            </div>}
            {!this.state.getInterviewList && <div>
              <h1 align="center" id='title'>{this.state.errorMesssge}</h1>
            </div>}
            </div>
          );
}

}




