import React, { Component } from "react"
import InterviewerDataService from '../../api/RestApi/InterviewerDataService.js'

import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap'

import "bootstrap/dist/css/bootstrap.min.css";
import '../../bootstrap.css';
import './FeedbackOfToday.css';
import { ModalTitle } from "react-bootstrap";
import UserDataService from "../../api/RestApi/UserDataService.js";

 class InterviewOfToday extends Component{

    constructor(props){
        super(props)

        this.state = {
            candidates:[],
            candidateId: '',
            getInterviewList : false,
            nullMessage:'',
            roundnumber :'',
            candidateName :'',
            interviewerName:'',
            experience:'',
            jobRole:'',
            dateOfInterview:'',
           
            interviewResult:'',
            technicalSkill:'',
            educationSkill:'',
            organizationalSkill:'',
            communicationSkill:'',
            overallRating:'',
            review:'',
            round:'',

            errorMesssge: '',
            userId: sessionStorage.getItem("userId")
        }
    } 

    componentDidMount() {
        InterviewerDataService.interviewOfToday(this.state.userId).then(
          response =>{ 

            console.log(response)
              if(response.data.dataList.length === 0){
            this.setState({ 
            getInterviewList:false,
            nullMessage:'No interview scheduled for today'
          }) 
        }else{
            this.setState({ candidates: response.data.dataList ,
                getInterviewList:true}) 
        }
        }).catch(response=>{
              this.setState({ getInterviewList:false })
        })
    }

    render(){
        let feedbackRow = this.state.candidates.map((candidates) => {
            return (
                <tr key={candidates.candidateId}>
                    <td></td>
                        <td>{candidates.candidateName} </td>
                        <td x-ms-format-detection="none" >{candidates.contactNumber}</td>
                        <td>{candidates.qualification}</td>
                        <td>{candidates.interview.interviewDate}</td>
                        <td>{candidates.interview.interviewTime}</td>
                        <td>{candidates.experience}</td>
                        <td>{candidates.candidateDepartment}</td>
                        <td>{candidates.candidateDesignation}</td>
                        <td><a href={'http://localhost:8080/showresume/' + candidates.candidateId + ''} 
                        target="_blank" data-toggle="tooltip" title="View Resume">
                        <i class="fa fa-file fa-2x"></i></a></td>


                </tr>
            );
        });
            return (
                <div>
                     

                <div className="col-md-12 col-sm-12">

                  {!this.state.getInterviewList && <div>
                  <h1 align="center" id='title1'>{this.state.nullMessage}</h1>
                </div>}
                
                {this.state.getInterviewList && <div className="viewTodaysFeedbackTable">
                     
                    <table className="table" id="viewTodaysFeedbackTable">
                      <thead>
                        <tr >
                        <th align="center">Index </th>
                        <th align="center">Candidate Name </th>
                        <th align="center">Contact Number </th>
                        <th align="center"> Qualification </th>
                        <th align="center">Interview Date</th>
                        <th align="center">Interview Time</th>
                        <th align="center">Experience (In Years)</th>
                        <th align="center">Department </th>
                        <th align="center">Designation </th>   
                        <th align="center">Resume</th>
                          
                        </tr>
                      </thead>
                      <tbody>
                        {feedbackRow}
                      </tbody>
                    </table>
               </div>} 
                  </div>
            </div>
            
              )
        
    }

 }

 export default InterviewOfToday;