import React from "react"
import { Component } from "react"
import CandidateDataService from '../../api/RestApi/CandidateDataService.js'
import InterviewerDataService from '../../api/RestApi/InterviewerDataService.js'
import './ScheduledInterview.css'

import { BrowserRouter as Router, Route, Switch, Link } from 'react-router-dom';
import { Button,Modal,ModalBody,ModalFooter,ModalHeader } from 'reactstrap'
import Moment from 'react-moment';
class ScheduleInterview extends Component {
  constructor() {
    super();
    this.state = {
      candidates: [],
      candidateId: '',
      roundnumber :'',
      candidateName :'',
      interviewerName:'',
      experience:'',
      jobRole:'',
      dateOfInterview:'',
     
      interviewResult:'',
      technicalSkill:'',
        educationSkill:'',
        organizationalSkill:'',
        communicationSkill:'',
        overallRating:'',
        review:'',
        round:'',

        round1Disable:false,
        round2Disable:false,
        round3Disable:false,
        round4Disable:false,
        round5Disable:false,

      getCandidateList: false,
      modalroundwisefeedbackisOpen :false,
      errorMesssge: '',
    }
  }

  componentDidMount() {
    CandidateDataService.retrieveScheduleInterview().then(response => {
      if(response.data.dataList.length!=0){
      this.setState({
        candidates: response.data.dataList,
        getCandidateList: true,
        errorMesssge: '',
      })
      }else{
        this.setState({
          getCandidateList: false,
          errorMesssge: "Record Not Found.",
        })
      }

    }).catch(response => {
      this.setState({
        getCandidateList: false,
        errorMesssge: "Something Went Wrong.",
      })
    })

  }

  viewResume = (candidateId) => {
    CandidateDataService.getResume(candidateId).then().catch(error => console.error('Error:', error));
  }

  toggleRoundFeedbackModal = (candidateId,roundnumber) => {

    
    if(roundnumber===0){

      alert("No Feedback Available")
    }
    else{
      this.setState({ candidateId: candidateId ,roundnumber:roundnumber})
    InterviewerDataService.getRoundWiseFeedback(candidateId,roundnumber).then(response => {
      console.log(response.data.data);
       this.setState({
         candidateName:response.data.data.candidateName,
         interviewerName: response.data.data.interviewerName,
        experience:response.data.data.experience,
        jobRole:response.data.data.jobRole,
        dateOfInterview:response.data.data.dateOfInterview,

        interviewResult:response.data.data.interviewResult,

        technicalSkill:response.data.data.technicalSkill,
        educationSkill:response.data.data.educationSkill,
        organizationalSkill:response.data.data.organizationalSkill,
        communicationSkill:response.data.data.communicationSkill,
        overallRating:response.data.data.overallRating,
        review:response.data.data.review,
        round:response.data.data.round,

       
         modalroundwisefeedbackisOpen: !this.state.modalroundwisefeedbackisOpen, buttonDisable: true
      })
    })
  }
  }

  onRoundSelect=({ target: { value } })=>{
  console.log(value)

  if(value === "1"){
  this.setState({ roundnumber:1})

    InterviewerDataService.getRoundWiseFeedback(this.state.candidateId,1).then(response => {
      console.log(response.data);
        if(response.data.data.feedbackId === 0){
          this.checkNotRequired(1);
        }
       
        else{
       this.setState({
         candidateName:response.data.data.candidateName,
         interviewerName: response.data.data.interviewerName,
        experience:response.data.data.experience,
        jobRole:response.data.data.jobRole,
        dateOfInterview:response.data.data.dateOfInterview,

        interviewResult:response.data.data.interviewResult,

        technicalSkill:response.data.data.technicalSkill,
        educationSkill:response.data.data.educationSkill,
        organizationalSkill:response.data.data.organizationalSkill,
        communicationSkill:response.data.data.communicationSkill,
        overallRating:response.data.data.overallRating,
        review:response.data.data.review,
        round:response.data.data.round,

       
      })
      }
     })
    }

    else if(value === "2"){
      this.setState({ roundnumber:2})
    
        InterviewerDataService.getRoundWiseFeedback(this.state.candidateId,2).then(response => {
          console.log(response.data);
          if(response.data.data.feedbackId === 0){
    
              this.checkNotRequired(2);
            }
    
          else{
           this.setState({
             candidateName:response.data.data.candidateName,
             interviewerName: response.data.data.interviewerName,
            experience:response.data.data.experience,
            jobRole:response.data.data.jobRole,
            dateOfInterview:response.data.data.dateOfInterview,
    
            interviewResult:response.data.data.interviewResult,
    
            technicalSkill:response.data.data.technicalSkill,
            educationSkill:response.data.data.educationSkill,
            organizationalSkill:response.data.data.organizationalSkill,
            communicationSkill:response.data.data.communicationSkill,
            overallRating:response.data.data.overallRating,
            review:response.data.data.review,
            round:response.data.data.round,
    
           
          })
        }
      
         })
        
        }
        else if(value === "3"){
          this.setState({ roundnumber:3})
        
            InterviewerDataService.getRoundWiseFeedback(this.state.candidateId,3).then(response => {
              console.log(response.data);
              if(response.data.data.feedbackId === 0){
                  this.checkNotRequired(3);
       
              }
              else{
               this.setState({
                 candidateName:response.data.data.candidateName,
                 interviewerName: response.data.data.interviewerName,
                experience:response.data.data.experience,
                jobRole:response.data.data.jobRole,
                dateOfInterview:response.data.data.dateOfInterview,
        
                interviewResult:response.data.data.interviewResult,
        
                technicalSkill:response.data.data.technicalSkill,
                educationSkill:response.data.data.educationSkill,
                organizationalSkill:response.data.data.organizationalSkill,
                communicationSkill:response.data.data.communicationSkill,
                overallRating:response.data.data.overallRating,
                review:response.data.data.review,
                round:response.data.data.round,
        
               
              })
            }
             })
            }
            else  if(value === "4"){
              this.setState({ roundnumber:4})
            
                InterviewerDataService.getRoundWiseFeedback(this.state.candidateId,4).then(response => {
                    
                  console.log(response.data);
                    
                  if(response.data.data.feedbackId === 0){
                      this.checkNotRequired(4);
             
                  }
                  else{
                      
                   this.setState({
                     candidateName:response.data.data.candidateName,
                     interviewerName: response.data.data.interviewerName,
                    experience:response.data.data.experience,
                    jobRole:response.data.data.jobRole,
                    dateOfInterview:response.data.data.dateOfInterview,
            
                    interviewResult:response.data.data.interviewResult,
            
                    technicalSkill:response.data.data.technicalSkill,
                    educationSkill:response.data.data.educationSkill,
                    organizationalSkill:response.data.data.organizationalSkill,
                    communicationSkill:response.data.data.communicationSkill,
                    overallRating:response.data.data.overallRating,
                    review:response.data.data.review,
                    round:response.data.data.round,
            
                   
                  })
                  
                }
                 })
                }
                else  if(value === "5"){
                  this.setState({ roundnumber:5})
                
                    InterviewerDataService.getRoundWiseFeedback(this.state.candidateId,5).then(response => {
                      console.log(response.data);
                      if(response.data.data.feedbackId === 0){
                        alert("Feedback is not given yet");
                        this.setState({
                          candidateName:'',
                          interviewerName: '',
                         experience:'',
                         jobRole:'',
                         dateOfInterview:'',
                 
                         interviewResult:'',
                 
                         technicalSkill:'',
                         educationSkill:'',
                         organizationalSkill:'',
                         communicationSkill:'',
                         overallRating:'',
                         review:'',
                         round:'',
                        })
                                
                      }
                      else{
                       this.setState({
                         candidateName:response.data.data.candidateName,
                         interviewerName: response.data.data.interviewerName,
                        experience:response.data.data.experience,
                        jobRole:response.data.data.jobRole,
                        dateOfInterview:response.data.data.dateOfInterview,
                
                        interviewResult:response.data.data.interviewResult,
                
                        technicalSkill:response.data.data.technicalSkill,
                        educationSkill:response.data.data.educationSkill,
                        organizationalSkill:response.data.data.organizationalSkill,
                        communicationSkill:response.data.data.communicationSkill,
                        overallRating:response.data.data.overallRating,
                        review:response.data.data.review,
                        round:response.data.data.round,
                
                       
                      })
                    }
                     })
                    }
}
  checkNotRequired=(round)=>{
    console.log("inside ----------------",round)

    if(round === 1){
      this.setState({round2Disable:true,round3Disable:true, round4Disable: true,round5Disable:true})
    }
    else if(round === 2){
        this.setState({round3Disable:true, round4Disable: true,round5Disable:true})
    }
    else if(round === 3){
      this.setState({ round4Disable: true,round5Disable:true})
    }
    else if( round === 4){
      this.setState({round5Disable:true})
    }
  
  
    InterviewerDataService.getRoundWiseFeedback(this.state.candidateId,5).then(response => {
      console.log(response)
     
      if(response.data.data.feedbackId === 0){
        alert("No feedback is given yet");
       
      }
      else{
        if(response.data.data.round === round){
      this.setState({
        
        candidateName:response.data.data.candidateName,
        interviewerName: response.data.data.interviewerName,
       experience:response.data.data.experience,
       jobRole:response.data.data.jobRole,
       dateOfInterview:response.data.data.dateOfInterview,
  
       interviewResult:response.data.data.interviewResult,
  
       technicalSkill:response.data.data.technicalSkill,
       educationSkill:response.data.data.educationSkill,
       organizationalSkill:response.data.data.organizationalSkill,
       communicationSkill:response.data.data.communicationSkill,
       overallRating:response.data.data.overallRating,
       review:response.data.data.review,
       round:response.data.data.round,
      })
    }
    else{
      if(response.data.data.round === 1){
        alert("No feedback is given yet");
        this.setState({round2Disable:true,round3Disable:true, round4Disable: true,round5Disable:true,
          candidateName:'',
          interviewerName: '',
         experience:'',
         jobRole:'',
         dateOfInterview:'',
  
         interviewResult:'',
  
         technicalSkill:'',
         educationSkill:'',
         organizationalSkill:'',
         communicationSkill:'',
         overallRating:'',
         review:'',
         round:'',
        })
      }
  
      else if(response.data.data.round === 2){
        alert("No feedback is given yet");
          this.setState({round3Disable:true, round4Disable: true,round5Disable:true,
            candidateName:'',
            interviewerName: '',
           experience:'',
           jobRole:'',
           dateOfInterview:'',
   
           interviewResult:'',
   
           technicalSkill:'',
           educationSkill:'',
           organizationalSkill:'',
           communicationSkill:'',
           overallRating:'',
           review:'',
           round:'',})
      }
      else if(response.data.data.round === 3){
        alert("No feedback is given yet");
        this.setState({ round4Disable: true,round5Disable:true,
          candidateName:'',
          interviewerName: '',
         experience:'',
         jobRole:'',
         dateOfInterview:'',
  
         interviewResult:'',
  
         technicalSkill:'',
         educationSkill:'',
         organizationalSkill:'',
         communicationSkill:'',
         overallRating:'',
         review:'',
         round:'',})
      }
      else if( response.data.data.round === 4){
        alert("No feedback is given yet");
        this.setState({round5Disable:true,
        candidateName:'',
               interviewerName: '',
              experience:'',
              jobRole:'',
              dateOfInterview:'',
      
              interviewResult:'',
      
              technicalSkill:'',
              educationSkill:'',
              organizationalSkill:'',
              communicationSkill:'',
              overallRating:'',
              review:'',
              round:'',
            })
      }
    }
    }
    })
  }
checkRounds = ()=>{

  
  if(this.state.roundnumber === 1){
    this.setState({round2Disable:true,round3Disable:true,round4Disable:true,round5Disable:true})

  }
  if(this.state.roundnumber === 2){
    this.setState({round3Disable:true,round4Disable:true,round5Disable:true})

  }
  else if(this.state.roundnumber === 3){
    this.setState({round4Disable:true,round5Disable:true})

  }
  else if(this.state.roundnumber === 4){
    this.setState({round5Disable:true})

  }

}

  toggleCloseRoundFeedbackModal = () => {

    this.setState({ modalroundwisefeedbackisOpen: !this.state.modalroundwisefeedbackisOpen })
  }
  render() {

    let userRow = this.state.candidates.map((candidates, i) => {

      return (
        <tr>
          <td>{candidates.candidateName}</td>
          {/* <td>{candidates.address}</td> */}
          <td x-ms-format-detection="none" >{candidates.contactNumber}</td>
          <td>{candidates.qualification}</td>
          <td><Moment format="DD/MM/YYYY" date={candidates.dateOfBirth}></Moment></td>
          <td>{candidates.emailId}</td>
          <td align="center">{candidates.experience}</td>
          <td>{candidates.gender}</td>
          <td><Moment format="DD/MM/YYYY" date={candidates.interviewDate}></Moment></td>
          <td>{candidates.name}</td>
          <td><a href={'http://localhost:8080/showresume/' + candidates.candidateId + ''} target="_blank" data-toggle="tooltip" title="View Resume"><i class="fa fa-file fa-2x"></i></a></td>
          <td><Link to={`/RescheuleInterview/${candidates.candidateId}`} data-toggle="tooltip" title="Reschedule Interview"><i class="fa fa-undo fa-2x" ></i></Link> 
          &nbsp;<a type="button" onClick={this.toggleRoundFeedbackModal.bind(this, candidates.candidateId,candidates.round -1)} data-toggle="tooltip" title="Roundwise Feedback" className="fa fa-comments-o fa-2x"> </a></td>
          <Modal isOpen={this.state.modalroundwisefeedbackisOpen} onOpened={this.checkRounds} >
              <ModalHeader toggle={this.toggleCloseRoundFeedbackModal.bind(this)}>
                Round Wise Feedback
                <select className="roundSelect" onChange={this.onRoundSelect}>
                  <option>Select Round</option>
                  <option value="1" disabled={this.state.round1Disable}>Round 1</option>
                  <option value="2" disabled={this.state.round2Disable}>Round 2</option>
                  <option value="3" disabled={this.state.round3Disable}>Round 3</option>
                  <option value="4" disabled={this.state.round4Disable}>Round 4</option>
                  <option value="5" disabled={this.state.round5Disable}>Round 5</option>
                </select>
            </ModalHeader>
              <ModalBody>
                <div className="item form-group">
                  <label className="col-form-label col-md-4 col-sm-4 label-align"
                    htmlFor="candidateName"> Candidate Name</label>
                  <input type="text" name="candidateName" value={this.state.candidateName}
                     className="col-form-label col-md-7 col-sm-7" id="candidateName" autoComplete="off" />
                </div>

                <div className="item form-group">
                  <label className="col-form-label col-md-4 col-sm-4 label-align"
                    htmlFor="contactNumber"> Interviewer Name</label>
                  <input type="text" name="interviewerName" value={this.state.interviewerName}
                    className="col-form-label col-md-7 col-sm-7" id="interviewerName" autoComplete="off" />
                </div>

               

                <div className="item form-group">
                  <label className="col-form-label col-md-4 col-sm-4 label-align"
                    htmlFor="jobRole"> Job Role</label>
                  <input type="text" name="jobRole" value={this.state.jobRole}
                    className="col-form-label col-md-7 col-sm-7" id="jobRole" autoComplete="off" />
                </div>

                <div className="item form-group">
                  <label className="col-form-label col-md-4 col-sm-4 label-align"
                    htmlFor="dateOfInterview"> Date Of Interview</label>
                  <input type="date" name="dateOfInterview" value={this.state.dateOfInterview}
                   className="col-form-label col-md-7 col-sm-7" id="dateOfInterview" autoComplete="off" />
                </div>

                <div className="item form-group">
                  <label className="col-form-label col-md-4 col-sm-4 label-align"
                    htmlFor="interviewResult"> Interview Result</label>
                  <input type="text" name="interviewResult" value={this.state.interviewResult}
                   className="col-form-label col-md-7 col-sm-7" id="interviewResult" autoComplete="off" />
                </div>

                <div className="item form-group">
                  <label className="col-form-label col-md-4 col-sm-4 label-align"
                    htmlFor="experience"> Experience</label>
                  <input type="text" name="experience" value={this.state.experience}
                  className="col-form-label col-md-2 col-sm-2" id="experience" autoComplete="off"/>
                
                  <label className="col-form-label col-md-3 col-sm-3 label-align"
                    htmlFor="round"> Round</label>
                  <input type="text" name="round" value={this.state.round}
                   className="col-form-label col-md-2 col-sm-2" id="round" autoComplete="off" />
                </div>

               


                <div className="item form-group">
                  <label className="col-form-label col-md-4 col-sm-4 label-align"
                    htmlFor="technicalSkill"> Technical Skill</label>
                  <input type="text" name="technicalSkill" value={this.state.technicalSkill}
                   className="col-form-label col-md-2 col-sm-2" id="technicalSkill" autoComplete="off" />
              
              <label className="col-form-label col-md-3 col-sm-3 label-align"
                    htmlFor="educationSkill">Education Skill</label>
                  <input type="text" name="educationSkill" value={this.state.educationSkill}
                   className="col-form-label col-md-2 col-sm-2" id="educationSkill" autoComplete="off" />
                
                  
                </div>

                <div className="item form-group">
                  <label className="col-form-label col-md-4 col-sm-4 label-align"
                    htmlFor="communicationSkill"> Communication Skill</label>
                  <input type="text" name="communicationSkill" value={this.state.communicationSkill}
                   className="col-form-label col-md-2 col-sm-2" id="communicationSkill" autoComplete="off" />
               
               <label className="col-form-label col-md-3 col-sm-3 label-align"
                    htmlFor="organizationalSkill"> Organization Skill</label>
                  <input type="text" name="organizationalSkill" value={this.state.organizationalSkill}
                   className="col-form-label col-md-2 col-sm-2" id="organizationalSkill" autoComplete="off" />
                </div>

                <div className="item form-group">
                  <label className="col-form-label col-md-4 col-sm-4 label-align"
                    htmlFor="overallRating"> Overall Rating</label>
                  <input type="text" name="overallRating" value={this.state.overallRating}
                   className="col-form-label col-md-7 col-sm-7" id="dateOfInterview" autoComplete="off" />
                </div>

                <div className="item form-group">
                  <label className="col-form-label col-md-4 col-sm-4 label-align"
                    htmlFor="review"> Review</label>
                  <textarea name="review" value={this.state.review}
                   className="col-form-label col-md-7 col-sm-7" id="review" autoComplete="off" />
                </div>

                

              </ModalBody>
              <ModalFooter>
                <Button onClick={this.toggleCloseRoundFeedbackModal.bind(this)} type="button" className="btn btn-default" >Cancel</Button>
              </ModalFooter>
            </Modal>

        </tr>
      );
    });

    return (
      <div>

        <h1 align="center" id='titlescheduledinterview'>List Of Scheduled Interview </h1>
        <div className="col-md-12 col-sm-12">

          {!this.state.getCandidateList && <div>
            <h1 align="center" id='titlescheduledinterview'>{this.state.errorMesssge}</h1>


          </div>}

          {this.state.getCandidateList && <div className="ScheduledInterviewTable">
            <table className="table" id="ScheduledInterviewTable">
              <thead>
                <tr>
                  <th>Candidate Name </th>
                  {/* <th>Address </th> */}
                  <th align="center">Contact Number </th>
                  <th align="center">Qualification </th>
                  <th align="center">DOB </th>
                  <th align="center">EmailId </th>
                  <th align="center">Experience (In Years)</th>
                  <th align="center">Gender </th>
                  <th align="center">Interview Date</th>
                  <th align="center">Interviewer Name</th>
                  <th align="center">Resume</th>
                  <th align="center">Action</th>









                </tr>
              </thead>
              <tbody>
                {userRow}
              </tbody>
            </table>
          </div>}
        </div>

      </div>
    )
  }
}
export default ScheduleInterview;