import axios from 'axios'

class InterviewerDataService {

    retrievePenddingInterviewApproval(userId){
        return axios.get(`http://localhost:8080/getpenddingapproval/${userId}`);
    }

    acceptInterview(interviewId){
        return axios.get(`http://localhost:8080/acceptinterview/${interviewId}`)
    }

    rejectInterview(interviewId,candidateId){
        return axios.get(`http://localhost:8080/rejectinterview/${interviewId}/${candidateId}`)
    }

    getCandidate(candidateId){
        return axios.get(`http://localhost:8080/getcandidate/${candidateId}`)
     }

    getRoundWiseFeedback(candidateId,roundnumber){
        return axios.get(`http://localhost:8080/getRoundfeedback/${candidateId}/${roundnumber}`);
    }

    viewallfeedback(userId){
        return axios.get(`http://localhost:8080/viewallfeedback/${userId}`);
    }

    scheduledInterviewParticularInterviewer(userId){
        return axios.get(`http://localhost:8080/scheduledInterviewListforInterview/${userId}`);
    }
   
    todaysFeedback(userId){
        return axios.get(`http://localhost:8080/todaysfeedback/${userId}`);
    }

    yesterdaysFeedback(userId){
        return axios.get(`http://localhost:8080/yesterdayfeedback/${userId}`);
    }

    lastweeksFeedback(userId){
        return axios.get(`http://localhost:8080/lastweeksfeedback/${userId}`);
    }

    interviewOfToday(userId){
        return axios.get(`http://localhost:8080/todaysinterview/${userId}`)
    }

    interviewOfTomorrow(userId){
        return axios.get(`http://localhost:8080/tomorrowsinterview/${userId}`)
    }

    interviewOfYesterday(userId){
        return axios.get(`http://localhost:8080/yesterdaysinterview/${userId}`)
    }

    interviewOfNextweek(userId){
        return axios.get(`http://localhost:8080/nextweeksinterview/${userId}`)
    }

    interviewOfLastweek(userId){
        return axios.get(`http://localhost:8080/lastweeksinterview/${userId}`)
    }

    viewFeedbackById(candidateId){
        return axios.get(`http://localhost:8080/viewfeedbackbyid/${candidateId}`)
    }

    getInterviewByInterviewerId(userId){
        return axios.get(`http://localhost:8080/getInterviews/${userId}`)
    }

    getRejectedCandidateList(userId){
        return axios.get(`http://localhost:8080/listofrejectedcandidate/${userId}`)
    }

    getHiredCandidateList(userId){
        return axios.get(`http://localhost:8080/listofhiredcandidate/${userId}`)
    }

    getAcceptedCandidateList(userId){
        return axios.get(`http://localhost:8080/listofacceptedcandidate/${userId}`)
    }
    

}

export default new InterviewerDataService();