import React, { Component } from "react";
import TableOfDashboard from "./TableOfDashboard";
import ViewAllUserChart from "../charts/viewAllUserChart";
import "./AdminDashboard.css";

class AdminDashboard extends Component {
  render() {
    return (
      <>
        <React.Fragment>
          <h1 align="center" id="titleUserList">
            User List
          </h1>
          <div className="forScrollingChart">
            <ViewAllUserChart />
          </div>
          <div className="forScrolling">
            <TableOfDashboard />
          </div>

          
        </React.Fragment>
      </>
    );
  }
}
export default AdminDashboard;
