import axios from 'axios'

class RequirementDataService {

    retrieveAllRequirements(){
       return axios.get('http://localhost:8080/getrequirements');
    }

    retrieveSpecificRequirement(requirementGroup,requirementDepartment){
        return axios.get(`http://localhost:8080/getspecificrequirements/${requirementGroup}/${requirementDepartment}`)
    }
}


export default new RequirementDataService();