import React, { Component } from 'react';
import { Button } from 'react-bootstrap';
import Information from '../../api/RestApi/CandidateDataService.js'
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap'
import './DynamicAddCandidate.css'
import RequirementDataService from '../../api/RestApi/RequirementDataService'
import axios from 'axios'

export default class DynamicAddCandidate extends Component{
    constructor(){
        super();
        this.state={
           
            jobRequirementCounterId:'',
            responseJob:[],
            responseData:[],
            group:[],
            
            requirementGroup:'',
            candidateDepartment:'',
            candidateDesignation:'',
            requirmentName:'',
            requirementDepartment:'',
            requirementDesignation:'',
            exprianceRange:'',
            requirementCounter:'',
            
            pesDepartment:[],
            asicDepartment:[],
            aerospaceDepartment:[],
            department:[],
            designationArray:[],
            designation:[],
            requirments:[],
            modalrequirementisOpen:false,

            jobpesDepartment:[],
            jobasicDepartment:[],
            jobaerospaceDepartment:[],
            jobDesignation:[],
            digitalDepartment:[],
            pesDesignation:[],

        }
    }


    componentDidMount(){
        RequirementDataService.retrieveAllRequirements().then(response=>{this.handleJob(response)}).catch(response=>{this.ErrorJob(response)})
        Information.getInfomation().then(response=>{this.handleInformation(response)}).catch(response=>{this.handleError(response)})
       
    }

    handleJob(response){
        this.setState({
            responseJob:response.data.dataList
        })
      

        this.state.responseJob.map((job)=>{
           // console.log("Job=="+job.requirementGroup)
            if(job.requirementGroup==="PES"){
                this.setState({
                    jobpesDepartment:this.state.jobpesDepartment.concat([{ id: job.jobRequirementCounterId,text: job.requirementDepartment }]),
                     pesDesignation:this.state.pesDesignation.concat([{ id: job.jobRequirementCounterId, text: job.requirementDesignation }]),
                   
                 
                })
                    console.log("designation=="+this.state.pesDesignation.length)
            }
            if(job.requirementGroup==="ASIC"){
               
                this.setState({
                    jobasicDepartment:this.state.jobasicDepartment.concat([{ id: job.jobRequirementCounterId,text: job.requirementDepartment }]),
                    jobDesignation:this.state.jobDesignation.concat([{id:job.jobRequirementCounterId,text: job.requirementDesignation}])
                })
            //  console.log("length of asic"+this.state.jobDesignation.length)
            }
            if(job.requirementGroup==="Aerospace"){
                this.setState({
                    jobaerospaceDepartment:this.state.jobaerospaceDepartment.concat([{ id: job.jobRequirementCounterId,text: job.requirementDepartment }])
                })
            }
            // if(job.requirementDepartment==="Digital"){
            //     this.setState({
            //         digitalDepartment:this.state.pesDepartment.concat([{ id: job.jobRequirementCounterId, text: job.requirementDepartment }])
            //     })
            //     console.log("****digital"+this.state.digitalDepartment)
            // }
            if(job.requirementDepartment==="Engineer"){
                this.setState({
                    digitalDepartment:this.state.pesDepartment.concat([{ id: job.jobRequirementCounterId, text: job.requirementDepartment }])
                })
                //console.log("****digital"+this.state.digitalDepartment)
            }
            
            
        })
        
    }

    handleInformation(response){
    

        this.setState({
            responseData:response.data.dataList
        })
       

        
      this.state.responseData.map((information)=>{
        if(information.informationType==="Group"){
            
            this.setState({
            group:this.state.group.concat([{ id: information.informationId, text: information.informationName }])
            })
        }
        if(information.informationType==="Department"){
            if(information.groupName==="PES"){
                if(this.state.jobpesDepartment.length!=0){
                this.setState({
                    pesDepartment:this.state.jobpesDepartment
                })
               
            }
            } if(information.groupName==="ASIC"){
                
                if(this.state.jobasicDepartment.length!=0){
                this.setState({
                    asicDepartment:this.state.jobasicDepartment
                })
                console.log("====="+this.state.asicDepartment)
            }
            } if(information.groupName==="Aerospace"){
                this.setState({
                    aerospaceDepartment:this.state.jobaerospaceDepartment
                })
            }
        }

        if(information.informationType==="Designation"){
        

            this.setState({
            //   designationArray:this.state.designationArray.concat([{ id: information.informationId, text: information.informationName }])
                designationArray:this.state.digitalDepartment
            })
        }
    
        })
    }

    handleError=(response)=>{
        console.log("Error")
    }



    //Change Group
onChangeGroup=(event)=>{
    this.state.department=[]
    this.state.designation=[]
    this.setState({
        requirementGroup:event.target.value
    })
    this.state.requirementGroup=event.target.value
   console.log(""+this.state.requirementGroup);
   
   if(event.target.value==="PES"){
    this.setState({
     department:this.state.pesDepartment
    })
    console.log("departments==="+this.state.department)
}else if(event.target.value==="ASIC"){
 this.setState({
     department:this.state.asicDepartment
    })
}else if(event.target.value==="Aerospace"){
 this.setState({
     department:this.state.aerospaceDepartment
    })
}
    
}

//Change Department

onDepartmentChange=(event)=>{
    this.state.designation=[]
        this.setState({
            candidateDepartment:event.target.value,
        
    })

    if(event.target.value!=""){
        this.setState({
            designation:this.state.designationArray
        })
    }
    
    this.state.requirementDepartment=event.target.value
    
    this.togglerequirementModal( this.state.requirementGroup, this.state.requirementDepartment)
    

    }

    togglerequirementModal = (requirementGroup,requirementDepartment) => {

        this.setState({ requirementGroup: requirementGroup,requirementDepartment: requirementDepartment})
        
          this.getUserData(requirementGroup,requirementDepartment);
          this.setState({
            modalrequirementisOpen: !this.state.modalrequirementisOpen
        })
      
      }

      getUserData = (requirementGroup,requirementDepartment) => {
        RequirementDataService.retrieveSpecificRequirement(requirementGroup,requirementDepartment).then(
          response => {
            console.log(response);
            this.setState({
                requirments : response.data.dataList,
                jobRequirementCounterId:response.data.dataList.jobRequirementCounterId,
               requirementGroup: response.data.dataList.requirementGroup, requirementDepartment: response.data.dataList.requirementDepartment,
                requirementDesignation: response.data.dataList.requirementDesignation, requirmentName: response.data.dataList.requirmentName,
                exprianceRange: response.data.dataList.exprianceRange,requirementCounter:response.data.dataList.requirementCounter,
                 buttonDisable:true
            })
              
          }
        )
      }

      toggleCloseRecruitmentModal = () => {

        this.setState({ modalrequirementisOpen: !this.state.modalrequirementisOpen })
      }

//Change Designation
onDesignationChange=(event)=>{
    this.setState({
        candidateDesignation:event.target.value
    })
    this.state.candidateDesignation=event.target.value
    console.log(this.state.candidateDesignation)

}
    render() {

        let userRow = this.state.requirments.map((requirments) => {
            return (
              
              <tr key={requirments.jobRequirementCounterId}>
                <td>{requirments.requirementGroup}</td>
                <td>{requirments.requirementDepartment}</td>
                <td>{requirments.requirementDesignation}</td>
                <td>{requirments.requirmentName}</td>
                <td>{requirments.requirementCounter}</td>
                <td>{requirments.exprianceRange}</td>
                <td><input type="radio" className="btn btn-success"></input></td>
              </tr>
            );
          });
           
            return (
                
                    <>
                             <div className="row ">
                           
                           <div className="col-md-6">
                                   <label htmlFor="group">Select Group <span className="error">*</span></label>
                                   <div>
                                       <select id="group" className="group" onChange={this.onChangeGroup}>
                                       <option value="">Select Group</option>
                                       {this.state.group.map(group => <option key={group.id} value={group.text}>{group.text}</option>)} 
     
   
                                       </select>
                                   </div>
                                    </div>
                     
                                    <div className="col-md-6">
                                    <label htmlFor="group">Select department <span className="error">*</span></label>
                                   <div>
                                   <select id="department" className="department" onChange={this.onDepartmentChange}>

                                    <option value="">Select Department </option>
                                    {this.state.department.map(department => <option key={department.id} value={department.text}>{department.text}</option>)}

   
                                       </select>
                                   </div>
                                  
                                    </div>

                           
                           </div>
            <Modal size="lg" style={{maxWidth: '2000px', width: '50%'}} isOpen={this.state.modalrequirementisOpen} >
            <ModalHeader toggle={this.toggleCloseRecruitmentModal.bind(this)}>
              Requirement Data
            </ModalHeader>
            <ModalBody>
            <table className="table" id="viewRequirementTable">
            <thead>
              <tr >
                <th > Group </th>
                <th > Department </th>
                <th > Designation </th>
                <th > Skill Set </th>
                <th > Number Of Openings </th>
                <th > Experience </th>
                <th > Action </th>


                
                
              </tr>
            </thead>
            <tbody>
              {userRow}
            </tbody>
          </table>

            </ModalBody>
            <ModalFooter>
              {/* <Button onClick={this.UpdateUser.bind(this)} disabled={this.state.buttonDisable} type="submit" className="btn btn-success " > Update </Button>
              <Button onClick={this.toggleCloseupdateModal.bind(this)} type="button" className="btn btn-default" >Cancel</Button> */}
            </ModalFooter>
          </Modal>
          

                           {/* <div className="row ">
                        <div className="col-md-6">
                            <label htmlFor="Designation">Select Designation <span className="error">*</span></label>
                                <div>
                                    <select id="Designation" className="Designation" onChange={this.onDesignationChange}>

                                        <option value="">Select Designation </option>
                                        {this.state.designation.map(designation => <option key={designation.id} value={designation.text}>{designation.text}</option>)}


                                    </select>
                                </div>
                            </div>
                        </div> */}

                    </>
                
            )
            }
}
