class AuthService {

    saveUser(username,name){
        sessionStorage.setItem("username",username)
        sessionStorage.setItem("name",name)

    }

    checkUser(){
        let user=  sessionStorage.getItem("username");
        // console.log("The User is")
        // console.log(user)
        if(user===null) {
       
            return false;
        }else{
        return true;
            }
        
    }
    isUserLogin(isUserLogin){
        
        if(isUserLogin===true){
           return true
        }else{
            return false;
        }
        
    }
    removeUser(){
        sessionStorage.removeItem("username");
        sessionStorage.removeItem("userType");
        sessionStorage.removeItem("userId");
        sessionStorage.removeItem("name")
       
        
    }
    saveUserType(user_type,userId){
        sessionStorage.setItem("userType",user_type)
        sessionStorage.setItem("userId",userId)
        
    }

    
    isHumanResource(){
        let user=  sessionStorage.getItem("userType");
        // console.log("The User is")
        // console.log(user)

        if(user==="HumanResource") {
       
            return true;
        }else{
        return false;
            }
    }
    isAdmin(){
        let user=  sessionStorage.getItem("userType");
        // console.log("The User is")
        // console.log(user)
        if(user==="Admin") {
       
            return true;
        }else{
        return false;
            }
    }

    isInterviewer(){
        let user=  sessionStorage.getItem("userType");
        // console.log("The User is")
        // console.log(user)
        if(user==="Interviewer") {
       
            return true;
        }else{
        return false;
            }
    }
    isManager(){
        let user=  sessionStorage.getItem("userType");
        // console.log("The User is")
        // console.log(user)
        if(user==="Manager") {
       
            return true;
        }else{
        return false;
            }
    }


    isValidUser(){
        let user=  sessionStorage.getItem("userType");
        if(user==="HumanResource"){
            return true;
        }else if(user==="Admin"){
            return true;
        }else if(user==="Interviewer"){
            return true;
        }
        else if(user==="Manager"){
            return true;
        }else{
            return false;
        }
    }
}
export default new AuthService();
