import React, { Component } from 'react';
import {HorizontalBar} from 'react-chartjs-2';
import InterviewerDataService from '../../api/RestApi/InterviewerDataService.js'
import './viewAllUserChart.css'

export default class FeedbackFilterHorizontalChart extends React.Component {

constructor(){
    super();

    this.state = {
        feedbacks :[],
        feedbackId:'',
        candidateId: '',
        getFeedbackList : false,

        userId: sessionStorage.getItem("userId"),
        statusfeedback:'',
        errorMesssge: '',
        interviewResult:'',
        feedbackAccept:0,
        feedbackReject:0,
        feedbackHire:0,
        totalCount:0,

    }
}

componentDidMount() {
    
        InterviewerDataService.viewallfeedback(this.state.userId).then(
            response => {
      
              if(response.data.dataList.length !=0){
      
                 
                  this.setState({ 
                      feedbacks: response.data.dataList ,
                      getFeedbackList:true
                    }) 
              }
                 else{
                  this.setState({ feedbacks: response.data.dataList ,
                      getFeedbackList:false,
                      errorMesssge:'No Feedback is Given Yet'
                    }) 
              }
          }).catch(response=>{
                this.setState({ getFeedbackList:false,
              errorMesssge: "Something Went Wrong.", })
          })
    
 }

render(){

    let feedbackRow = this.state.feedbacks.map((feedbacks) => {

      if(feedbacks.feedbackId!=null && feedbacks.interviewResult ==="Accept"){
        this.state.feedbackAccept=this.state.feedbackAccept + 1;
      }

      if(feedbacks.feedbackId!=null && feedbacks.interviewResult==="Reject"){
        this.state.feedbackReject=this.state.feedbackReject + 1;
      }

      if(feedbacks.feedbackId!=null && feedbacks.interviewResult==="Hire"){
        this.state.feedbackHire=this.state.feedbackHire + 1;
      }
       
          return (
  
              this.state.statusfeedback={
                  labels: ['Accept','Reject','Hire'],
              datasets: [
              {
              label: 'Feedback',
              backgroundColor: [
              '#008000',
              '#B21F00',
              '#2FDE00',
              '#00A6B4',
              '#6800B4'
              ],
              hoverBackgroundColor: [
              
              '#32CD32',
              '#501800',
              '#175000',
              '#003350',
              '#35014F'
              ],
              data: [this.state.feedbackAccept,this.state.feedbackReject,this.state.feedbackHire],
             
              }
                ]
               }
          
          );
        });

        
        return (
          <div> <h1 align="center" id='titlechart'>Feedback Chart</h1>
            {this.state.getFeedbackList && <div>
             
               <HorizontalBar
                data={this.state.statusfeedback}
                options={{
                  responsive: true, 
                  maintainAspectRatio: true,

                  title:{
                    display:true,
                    text:' ',
                    fontSize:20
                  },
                  legend:{
                    display:false,
                    position:'right'
                  },
                  scales: {
                    xAxes: [{
                      ticks: {
                        min: 0
                      }
                    }]
                  }
                }} 
              />
              
            </div>}
            {!this.state.getFeedbackList && <div>
              <h1 align="center" id='title'>{this.state.errorMesssge}</h1>
            </div>}
            </div>
            
          );
}

}




